import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import EditIcon from '@material-ui/icons/Edit';
import { LinkContainer } from 'react-router-bootstrap';
import { useAlert } from "react-alert";
import VisibilityIcon from '@material-ui/icons/Visibility';

// components
import PageTitle from "../../../components/PageTitle";

import TableOptions from '../../../config/table';

import api from '../../../services/api';

export default function() {
  const alert = useAlert();
  const [loading, setLoading] = useState(true);
  const [users, setUsers] = useState([]);
  const [pagination, setPagination] = useState({});

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },    
    {  
      name: 'name', 
      label: 'Nome',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'email', 
      label: 'E-mail',
      options: {
        filterType: 'textField'
      }
    },    
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta) => {
          const selected = users[tableMeta.rowIndex];
          return (
            <>
              <LinkContainer to={`/app/user/detail/${selected.id}`}>
                <Button>
                    <VisibilityIcon />
                </Button>
              </LinkContainer>
              <LinkContainer to={`/app/user/edit/${selected.id}`}>
                <Button>
                    <EditIcon />
                </Button>
              </LinkContainer>
              <Button onClick={async () => {
                if (window.confirm('Remover esse item?')) {
                  await deleteItem(selected.id);
                  users.splice(tableMeta.rowIndex, 1);
                  setUsers(users.slice());
                  alert.success('Item excluido com sucesso!');
                }
              }}>
                <HighlightOffIcon />
              </Button>              
            </>
          );
        }
      }
    }    
  ];

  let itemsSelecteds = [];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: pagination.total || 0,
      onTableChange: (action, tableState) => {
        if (action === 'rowsSelect') {
          itemsSelecteds = [];

          tableState.selectedRows.data.map(data => {
            const selected = users[data.index];
            itemsSelecteds.push(selected);
            return data;
          });
        }

        if (action === 'rowDelete') {
          itemsSelecteds.map(user => {
            deleteItem(user.id);
            alert.success('Registros selecionados excluidos com sucesso!');
            return null;
          });
        }
      },
  };

  const deleteItem = async (id) => {
    await api.delete(`/user/${id}`);
  }

  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/user`, {
        params: {
          perPage: 999999
        }
      });

      setUsers(data.data);
      setPagination(data);
      setLoading(false);
    }

    fetchData();
  }, []);

  return (
    <>
      <PageTitle title="Usuários">
          <LinkContainer to="/app/user/create">
            <Button variant="contained" size="large" color="secondary">
              Cadastrar
            </Button>
          </LinkContainer>
      </PageTitle>

      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

      {!loading && 
        <MUIDataTable title="Lista de Usuários" data={users} columns={columns} options={options} />
      }
    </>
  );
}
