import React from "react";
import { Route, Switch, withRouter } from "react-router-dom";

import Dashboard from "./pages/dashboard";

import ProductList from './pages/products/List';
import ProductCreate from './pages/products/Create';
import ProductEdit from './pages/products/Edit';
import ProductAddOptions from './pages/products/AddOptions';

import CategoryList from './pages/categories/List';
import CategoryCreate from './pages/categories/Create';
import CategoryEdit from './pages/categories/Edit';

import { UserList, UserCreate, UserEdit, UserDetail } from './pages/users';
import { BannerList, BannerCreate, BannerEdit } from './pages/banners';
import { PaymentMethodList, PaymentMethodCreate, PaymentMethodEdit } from './pages/paymentMethods';
import { OrderStateList, OrderStateCreate, OrderStateEdit } from './pages/OrderState';
import { OrderList, OrderDetail } from './pages/order';
import { PageList, PageCreate, PageEdit } from './pages/pages';
import { SettingList, SettingCreate, SettingEdit } from './pages/settings';
import { NeighborhoodList, NeighborhoodCreate, NeighborhoodEdit } from './pages/neighborhoods';
import { CreditRuleList, CreditRuleCreate, CreditRuleEdit } from './pages/credit-rules';
import { VoucherList, VoucherCreate, VoucherEdit } from './pages/vouchers';

function Layout(props) {

  return (
    <Switch>
        <Route path="/app/product/list" component={ProductList} />
        <Route path="/app/product/create" component={ProductCreate} />
        <Route path="/app/product/edit/:id" component={ProductEdit} />
        <Route path="/app/product/add-options/:id" component={ProductAddOptions} />

        <Route path="/app/category/list" component={CategoryList} />
        <Route path="/app/category/create" component={CategoryCreate} />
        <Route path="/app/category/edit/:id" component={CategoryEdit} />

        <Route path="/app/user/list" component={UserList} />
        <Route path="/app/user/create" component={UserCreate} />
        <Route path="/app/user/edit/:id" component={UserEdit} />
        <Route path="/app/user/detail/:id" component={UserDetail} />

        <Route path="/app/banner/list" component={BannerList} />
        <Route path="/app/banner/create" component={BannerCreate} />
        <Route path="/app/banner/edit/:id" component={BannerEdit} />

        <Route path="/app/payment-method/list" component={PaymentMethodList} />
        <Route path="/app/payment-method/create" component={PaymentMethodCreate} />
        <Route path="/app/payment-method/edit/:id" component={PaymentMethodEdit} />

        <Route path="/app/order-state/list" component={OrderStateList} />
        <Route path="/app/order-state/create" component={OrderStateCreate} />
        <Route path="/app/order-state/edit/:id" component={OrderStateEdit} />

        <Route path="/app/dashboard" component={Dashboard} />
        <Route path="/app/order/detail/:id" component={OrderDetail} />
        <Route path="/app/order" component={OrderList} />

        <Route path="/app/page/list" component={PageList} />
        <Route path="/app/page/create" component={PageCreate} />
        <Route path="/app/page/edit/:id" component={PageEdit} />

        <Route path="/app/setting/list" component={SettingList} />
        <Route path="/app/setting/create" component={SettingCreate} />
        <Route path="/app/setting/edit/:id" component={SettingEdit} />

        <Route path="/app/neighborhood/list" component={NeighborhoodList} />
        <Route path="/app/neighborhood/create" component={NeighborhoodCreate} />
        <Route path="/app/neighborhood/edit/:id" component={NeighborhoodEdit} />

        <Route path="/app/credit-rule/list" component={CreditRuleList} />
        <Route path="/app/credit-rule/create" component={CreditRuleCreate} />
        <Route path="/app/credit-rule/edit/:id" component={CreditRuleEdit} />

        <Route path="/app/voucher/list" component={VoucherList} />
        <Route path="/app/voucher/create" component={VoucherCreate} />
        <Route path="/app/voucher/edit/:id" component={VoucherEdit} />
    </Switch>
  );
}

export default withRouter(Layout);
