import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import Loading from '../../../components/Loading';
import Form from '../Form';

import api from '../../../services/api';

export default function(){
    const [creditRule, setCreditRules] = useState({});
    const { id } = useParams();

    useEffect(() => {
        async function fetchData() {
            const { data } = await api.get(`/creditRule/${id}`);
            setCreditRules(data);
        }

        fetchData();
    }, [id])


    if (!creditRule.id) return <Loading />;

    return <Form creditRule={creditRule} pageTitle="Regras de Créditos" />;

}
