import React, { useState, useEffect } from 'react';
import { Redirect } from 'react-router-dom';
import { useAlert } from "react-alert";
import { useTheme } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import objectToFormData from 'object-to-formdata';
import PageTitle from "../../../components/PageTitle";
import useStyles from './styles';

import MenuItem from '@material-ui/core/MenuItem';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Chip from '@material-ui/core/Chip';

import api from '../../../services/api';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
    },
  },
};

function getStyles(name, personName, theme) {
  return {
    fontWeight:
      personName.indexOf(name) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}

export default () => {
  const alert = useAlert();
  const [name, setName] = useState('')
  const [price, setPrice] = useState('')
  const [description, setDescription] = useState('')
  const [image, setImage] = useState('')
  const [categories, setCategories] = useState('')
  const [categoriesSelecteds, setCategoriesSelecteds] = useState([])
  const [redirect, setRedirect] = useState(false)

  const classes = useStyles();
  const theme = useTheme();

  useEffect(() => {
    async function getCategories() {
      const { data } = await api.get('/category/all');
      setCategories(data);
    }

    getCategories();
  }, [])
  
  const onSubmit = async (e) => { 
    try {
        const options = { indices: true, nullsAsUndefineds: false };
        const product = {name, price, description, image, categoriesSelecteds}
        const formData = objectToFormData(product, options)
        const { data } = await api.post('/product', formData)

        alert.success('Salvo com sucesso!');
        setRedirect(data.id);
    } catch (error) {
      console.log( error )
    }

    return false;
  }

  return (
    <div className={classes.root}>
      
      {redirect && <Redirect to={`/app/product/add-options/${redirect}`} />}

      <PageTitle title="Informações do Produto" />

        <form onSubmit={onSubmit}> 
          <Paper className={classes.paper}>

              <TextField 
                  className={classes.textField}
                  label="Informe o nome do Produto" 
                  name="name"
                  onChange={(e) => {
                    setName(e.target.value);
                  }}
              />

              <TextField 
                  className={classes.textField}
                  label="Preço do Produto" 
                  name="name"
                  onChange={(e) => {
                    setPrice(e.target.value);
                  }}
              />

              <TextField 
                  className={classes.textField}
                  multiline
                  rows="4"
                  label="Descrição" 
                  onChange={(e) => {
                    setDescription(e.target.value);
                  }}
              />

              <TextField 
                  className={classes.textField}
                  label="Imagem Destaque" 
                  type="file"
                  onChange={(e) => {
                    setImage(e.target.files[0]);
                  }}
                  InputLabelProps={{
                    shrink: true,
                  }}                  
              />

              {categories.length > 0 && <FormControl className={classes.formControl}>
                <InputLabel>Categorias Relacionadas</InputLabel>
                <Select
                  labelId="demo-mutiple-chip-label"
                  id="demo-mutiple-chip"
                  multiple
                  value={categoriesSelecteds}
                  onChange={(e) => {
                      setCategoriesSelecteds(e.target.value);
                      console.log(e.target)
                  }}
                  input={<Input id="select-multiple-chip" />}
                  renderValue={selected => (
                    <div className={classes.chips}>
                      {selected.map(value => (
                          <Chip key={value} label={categories.find(i => i.id === value).name} className={classes.chip} />
                      ))}
                    </div>
                  )}
                  MenuProps={MenuProps}
                >
                  {categories.map(category => (
                    <MenuItem key={category.id} value={category.id} style={getStyles(name, categoriesSelecteds, theme)}>
                      {category.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              }           
            </Paper>

          <div className={classes.buttons}>
            <Button  variant="contained">
              <ArrowBackIosIcon /> Voltar
            </Button>
            <Button type="button" onClick={() => onSubmit()} variant="contained" color="primary">
              Cadastrar <ArrowForwardIosIcon />
            </Button>
          </div>
        </form>
    </div>
  );

}