import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import EditIcon from '@material-ui/icons/Edit';
import { LinkContainer } from 'react-router-bootstrap';
import { useAlert } from "react-alert";

// components
import PageTitle from "../../../components/PageTitle";

import TableOptions from '../../../config/table';

import api from '../../../services/api';

export default function() {
  const alert = useAlert();
  const [loading, setLoading] = useState(true);
  const [paymentMethods, setpaymentMethods] = useState([]);

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },    
    {  
      name: 'name', 
      label: 'Nome',
      options: {
        filterType: 'textField'
      }
    },
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta) => {
          const selected = paymentMethods[tableMeta.rowIndex];
          return (
            <>
              <LinkContainer to={`/app/payment-method/edit/${selected.id}`}>
                <Button>
                    <EditIcon />
                </Button>
              </LinkContainer>
              <Button onClick={async () => {
                if (window.confirm('Remover esse item?')) {
                  await deleteItem(selected.id);
                  paymentMethods.splice(tableMeta.rowIndex, 1);
                  setpaymentMethods(paymentMethods.slice());
                  alert.success('Item excluido com sucesso!');
                }
              }}>
                <HighlightOffIcon />
              </Button>              
            </>
          );
        }
      }
    }    
  ];

  let itemsSelecteds = [];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: paymentMethods.length,
      onTableChange: (action, tableState) => {
        if (action === 'rowsSelect') {
          itemsSelecteds = [];

          tableState.selectedRows.data.map(data => {
            const selected = paymentMethods[data.index];
            itemsSelecteds.push(selected);
            return data;
          });
        }

        if (action === 'rowDelete') {
          itemsSelecteds.map(paymentMethod => {
            deleteItem(paymentMethod.id);
            alert.success('Registros selecionados excluidos com sucesso!');
            return null;
          });
          
        }
      },
  };

  const deleteItem = async (id) => {
    await api.delete(`/payment-method/${id}`);
  }

  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/paymentMethod`);

      setpaymentMethods(data);
      setLoading(false);
    }

    fetchData();
  }, []);

  return (
    <>
      <PageTitle title="Métodos de Pagamento">
          <LinkContainer to="/app/payment-method/create">
            <Button 
                variant="contained"
                size="large"
                color="secondary"
            >
              Cadastrar
            </Button>
          </LinkContainer>
      </PageTitle>

      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

      {!loading && 
        <MUIDataTable
          title="Lista de métodos de pagamento"
          data={paymentMethods}
          columns={columns}
          options={options}
        />
      }
    </>
  );
}
