import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button, Chip } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import EditIcon from '@material-ui/icons/Edit';
import { LinkContainer } from 'react-router-bootstrap'
import { useAlert } from "react-alert";

// components
import PageTitle from "../../../components/PageTitle";

import TableOptions from '../../../config/table';

import api from '../../../services/api';

const ReactIntl = require('react-intl');
const FormattedNumber = ReactIntl.FormattedNumber;

export default function Tables() {
  const alert = useAlert();
  const [loading, setLoading] = useState(true);
  const [products, setProducts] = useState([]);

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },    
    {  
      name: 'name', 
      label: 'Nome',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'price', 
      label: 'Preço',
      options: {
        filter: true,
        filterType: 'textField',
        customBodyRender: (value) => {
          const Money = ({ value, style="currency" }) => (
            <FormattedNumber style={style} value={value} currency="BRL" />
          )

          return Money({ value });
        }
      } 
    },
    {
      name: 'categories', 
      label: 'Categorias',
      options: {
        filter: true,
        filterType: 'multiselect',
        responsive: 'scrollMaxHeight',
        customBodyRender: (value) => {
          return value.map( (val, key) => {
              return <Chip label={val} key={key} />;
          });
        },
        customFilterListOptions: { render: v => `Salary: ${v}` },
      } 
    },
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          const selected = products[tableMeta.rowIndex];
          return (
            <>
              <LinkContainer to={`/app/product/edit/${selected.id}`}>
                <Button>
                    <EditIcon />
                </Button>
              </LinkContainer>
              <Button onClick={async () => {
                if (window.confirm('Remover esse item?')) {
                  await deleteItem(selected.id);
                  products.splice(tableMeta.rowIndex, 1);
                  setProducts(products.slice());
                  alert.success('Item excluido com sucesso!');
                }
              }}>
                <HighlightOffIcon />
              </Button>              
            </>
          );
        }
      }
    }    
  ];

  let itemsSelecteds = [];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: products.length || 0,
      onTableChange: (action, tableState) => {
        if (action === 'rowsSelect') {
          itemsSelecteds = [];

          tableState.selectedRows.data.map(data => {
            const selected = products[data.index];
            itemsSelecteds.push(selected);
            return data;
          });
        }

        if (action === 'rowDelete') {
          itemsSelecteds.map(product => {
            deleteItem(product.id);
            return null;
          });

          alert.success('Registros selecionados excluidos com sucesso!');
        }
      },
  };

  const deleteItem = async (id) => {
    await api.delete(`/product/${id}`);
  }

  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/product`);

      const listProducts = [];

      data.map(product => {
          const listCategories = [];
          product.categories.map(cat => listCategories.push(cat.name));
          product.categories = listCategories;
          listProducts.push(product);
          return product;
      });

      setProducts(listProducts);
      setLoading(false);
    }

    fetchData();
  }, []);

  return (
    <>
      <PageTitle title="Produtos">
          <LinkContainer to="/app/product/create">
            <Button variant="contained" size="large" color="secondary">
              Cadastrar
            </Button>
          </LinkContainer>
      </PageTitle>

      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

      {!loading && 
        <MUIDataTable
          title="Lista de Produtos"
          data={products}
          columns={columns}
          options={options}
        />
      }
    </>
  );
}
