import React, { useState, useEffect } from 'react'
import { useAlert } from "react-alert";
import TextField from '@material-ui/core/TextField';
import Fab from '@material-ui/core/Fab';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import AddIcon from '@material-ui/icons/Add';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';

import PageTitle from "../../../components/PageTitle";
import Loading from '../../../components/Loading';
import useStyles from './styles';

import api from '../../../services/api';

export default ({ product }) => {
  const alert = useAlert();
  const classes = useStyles();
  const [options, setOptions] = useState([]);

  useEffect(() => {
    async function ferchData() {
        const { data } = await api.get(`/product/${product.id}/option`);
        setOptions(data);

        console.log(data)
    }

    ferchData();
  }, [product]);

  async function createOption() {
    const { data } = await api.post(`/product/${product.id}/option`);
    options.push(data)
    setOptions(options.slice());
  }

  async function deleteOption(option) {
    await api.delete(`/product/${product.id}/option/${option.id}`);
    options.splice(options.indexOf(option), 1);
    setOptions(options.slice());
    alert.success('Opção excluida.');
  }

  async function updateOption(option) {
    await api.patch(`/product/${product.id}/option/${option.id}`, option);
  }

  async function updateProductOption(data) {
    await api.patch(`/product/${product.id}`, data);
  }

  if (!options) return <Loading />;

  return (
    <div className={classes.root}>

      <PageTitle variant={'h3'} title={'Opções'} />

      <Grid container spacing={3}>
      
          <Grid item xs={6}>
            <Paper className={classes.paper} style={{ position: 'relative', paddingBottom: 60 }}>

              <Grid container style={{ paddingBottom: 20}}>
                <Grid item xs={12}>
                  <TextField 
                    className={classes.textField} label="Título das opções" 
                    defaultValue={product.options_title} 
                    placeholder="Ex: Selecione o Tamanho"
                    onChange={(e) => {product.options_title = e.target.value}}
                    onBlur={(e) => updateProductOption({ options_title: e.target.value })}
                  />
                </Grid>
              </Grid>

              {options.map((option, i) => 
                <Grid container key={i} spacing={0} style={{ marginBottom: 10 }}>
                  <Grid item xs={6}>
                    <TextField 
                      className={classes.textField} 
                      label="Nome" 
                      defaultValue={option.name} 
                      onChange={(e) => {option.name = e.target.value}} 
                      onBlur={() => updateOption(option)}
                    />
                  </Grid>
                  <Grid item xs={4}>
                    <TextField 
                      className={classes.textField} 
                      label="Preço" 
                      defaultValue={option.price} 
                      onChange={(e) => {option.price = e.target.value}} 
                      onBlur={() => updateOption(option)}
                    />
                  </Grid>

                  <Grid item xs={2}>
                    <Button onClick={() => deleteOption(option)}>
                      <RemoveCircleOutlineIcon />
                    </Button>
                  </Grid>

                </Grid>
                )}

                <Fab size="small" className={classes.fab} color="primary" onClick={() => createOption()}>
                  <AddIcon />
                </Fab>
            </Paper>
   
          </Grid>
      </Grid>
    </div>
  );

}