import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit';
import { LinkContainer } from 'react-router-bootstrap';

import PageTitle from "../../../components/PageTitle";
import TableOptions from '../../../config/table';
import api from '../../../services/api';

export default function() {
  const [loading, setLoading] = useState(true);
  const [pages, setPages] = useState([]);

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'title', 
      label: 'Página',
      options: {
        filterType: 'textField'
      }
    },
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta) => {
          const selected = pages[tableMeta.rowIndex];
          return (
            <>
              <LinkContainer to={`/app/page/edit/${selected.id}`}>
                <Button>
                    <EditIcon />
                </Button>
              </LinkContainer>
            </>
          );
        }
      }
    }    
  ];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: pages.length,
  };

  const deleteItem = async (id) => {
    await api.delete(`/page/${id}`);
  }

  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/page`);

      setPages(data);
      setLoading(false);
    }

    fetchData();

    return () => {}
  }, []);

  return (
    <>
      <PageTitle title="Páginas">
          <LinkContainer to="/app/page/create">
            <Button variant="contained" size="large" color="secondary">Cadastrar</Button>
          </LinkContainer>
      </PageTitle>

      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

      {!loading && 
        <MUIDataTable
          title="Lista de páginas"
          data={pages}
          columns={columns}
          options={options}
        />
      }
    </>
  );
}
