import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { useAlert } from "react-alert";
import DialogTitle from '@material-ui/core/DialogTitle';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

import useStyles from './styles';
import { Grid } from '@material-ui/core';

export default function({ group, showSettings, updateGroup }) {
  const alert = useAlert();
  const classes = useStyles();

  return (
      <Dialog open={group.open || false} onClose={() => showSettings(group)}>
          <DialogTitle>Configurações</DialogTitle>
          <DialogContent>

              <FormControl className={classes.formControl}>     
                <TextField 
                    className={classes.textField}
                    label="Descrição" 
                    name="description"
                    defaultValue={group.description}
                    onChange={e => group.description = e.target.value}
                />
              </FormControl>

              <FormControl className={classes.formControl}>     
                <TextField 
                    className={classes.textField}
                    label="Regras" 
                    name="rules"
                    defaultValue={group.rules}
                    onChange={e => group.rules = e.target.value}
                />
              </FormControl>

              <Grid container>
                <Grid item>
                  <FormControl className={classes.formControl}>     
                    <TextField 
                        className={classes.textField}
                        label="Máximo" 
                        name="max"
                        defaultValue={group.max}
                        onChange={e => group.max = e.target.value}
                    />
                  </FormControl>
                </Grid>

                <Grid item>
                  <FormControl className={classes.formControl}>     
                    <TextField 
                        className={classes.textField}
                        label="Mínimo" 
                        name="min"
                        defaultValue={group.min}
                        onChange={e => group.min = e.target.value}
                    />
                  </FormControl>
                </Grid>
              </Grid>

          </DialogContent>
          <DialogActions>
            <Button 
              onClick={() => {
                updateGroup(group);
                showSettings(group);
                alert.success('Configurações atualizadas.');
              }}
              color="primary" 
              autoFocus
            >
              Salvar
            </Button>
          </DialogActions>
      </Dialog>    
  )
}