import React from 'react';
import { withFormik } from 'formik';
import * as Yup from 'yup';
import { FormControl, TextField, Button } from '@material-ui/core';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import CircularProgress from '@material-ui/core/CircularProgress';
import { LinkContainer } from 'react-router-bootstrap';
import objectToFormData from 'object-to-formdata';
import SaveIcon from '@material-ui/icons/Save';

import api from '../../../services/api';

import useStyles from './styles';

function Form(props) {
    const classes = useStyles();

    return (
      <form onSubmit={props.handleSubmit}>
          <FormControl className={classes.formControl}>
            <TextField 
                label="Nome" 
                onChange={e => props.setFieldValue('name', e.target.value)}
                value={props.values.name}
                name="name"
            />
            {props.touched.name && props.errors.name && 
              <div className={classes.textError}>{props.errors.name}</div>
            }
          </FormControl>

          <FormControl className={classes.formControl}>
            <TextField 
                label="Label" 
                onChange={e => props.setFieldValue('label', e.target.value)}
                value={props.values.label}
                name="label"
            />
            {props.touched.label && props.errors.label && 
              <div className={classes.textError}>{props.errors.label}</div>
            }
          </FormControl>

          <FormControl className={classes.formControl}>
            <TextField 
                label="Nome Público" 
                onChange={e => props.setFieldValue('public_name', e.target.value)}
                value={props.values.public_name}
                name="public_name"
            />
            {props.touched.public_name && props.errors.public_name && 
              <div className={classes.textError}>{props.errors.public_name}</div>
            }
          </FormControl>
          
          <FormControl className={classes.formControl}>
            <TextField 
                label="Valor" 
                onChange={e => props.setFieldValue('value', e.target.value)}
                value={props.values.value}
                name="value"
                rows="5"
                multiline
            />
            {props.touched.value && props.errors.value && 
              <div className={classes.textError}>{props.errors.value}</div>
            }
          </FormControl>

          <div className={classes.buttons}>
            <LinkContainer to={'/app/setting/list'}>
              <Button variant="contained">
                <ArrowBackIosIcon /> Voltar
              </Button>
            </LinkContainer>
            <Button 
              type="button" 
              onClick={props.handleSubmit} 
              variant="contained" 
              color="primary"
              disabled={props.isSubmitting}
            >
              Salvar 
              {!props.isSubmitting && <SaveIcon />}
              {props.isSubmitting && <CircularProgress className={classes.spinner} />}
            </Button>
          </div>          
      </form>
    )
}

export default withFormik({
  mapPropsToValues: props => ({ 
      id: props.setting ? props.setting.id : '',
      name: props.setting ? props.setting.name : '',
      label: props.setting ? props.setting.label : '',
      public_name: props.setting ? props.setting.public_name : '',
      value: props.setting ? props.setting.value : '',
    }),

  validationSchema: Yup.object().shape({
      name: 
        Yup.string()
        .required('Informe o nome da configuração'),
      label: 
        Yup.string()
        .required('Ese campo é obrigatório'),
      public_name: 
        Yup.string()
        .required('Ese campo é obrigatório'),
  }),

  handleSubmit: async (values, { setSubmitting, props, setErrors }) => {
      setSubmitting(true);
      
      const options = { indices: true, nullsAsUndefineds: false };
      const formData = objectToFormData(values, options);

      let data = {};
      
      if (!values.id) {
        data = await api.post('/setting', formData);
      } else {
        data = await api.patch(`/setting/${values.id}`, formData);
      }

      data = data.data;

      if (data.success) {
        return props.onSuccess(data);
      }

      if (data.validations) {
        data.validations.map(e => {
            let error = {};
            error[e.field] = e.message;
            setErrors(error);
        });
      }

      setSubmitting(false);
  },

})(Form);
