import React, { useState, useEffect } from 'react';
import { useAlert } from "react-alert";
import { Redirect } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Select from '@material-ui/core/Select';
import Paper from '@material-ui/core/Paper';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { withFormik } from 'formik';
import * as Yup from 'yup';
import Grid from '@material-ui/core/Grid';
import moment from 'moment';

import PageTitle from "../../../components/PageTitle";

import api from '../../../services/api';
import useStyles from './styles';
import { LinkContainer } from 'react-router-bootstrap';

function CreditRuleForm(props){
    const classes = useStyles();
    const alert = useAlert();
    const [redirect, setRedirect] = useState(false);

    useEffect(() => {
        if (props.creditRule) {
            props.setFieldValue('id', props.creditRule.id);
            props.setFieldValue('name', props.creditRule.name);
            props.setFieldValue('total_orders', props.creditRule.total_orders);
            props.setFieldValue('description', props.creditRule.description || '');
            props.setFieldValue('observation', props.creditRule.observation || '');
            props.setFieldValue('active', props.creditRule.active || 0);
            props.setFieldValue('date_start', new moment(props.creditRule.date_start).format('YYYY-MM-DD') );
            props.setFieldValue('date_end', new moment(props.creditRule.date_end).format('YYYY-MM-DD') );
        }
    }, []);

    useEffect(() => {
        if (props.status && props.status.success) {
            alert.show('Salvo com sucesso!');
            setRedirect(true);
        }
    }, [props.status]);

    if (redirect) return<Redirect to={`/app/credit-rule/list`} />

    return (
        <div className={classes.root}>
        
            <PageTitle title={props.pageTitle} />
            
            <form onSubmit={props.handleSubmit}>
                <Paper className={classes.paper}>

                    <FormControl className={classes.formControl}>
                        <InputLabel>Status</InputLabel>
                        <Select
                            native
                            value={props.values.active}
                            onChange={e => props.setFieldValue('active', e.target.value)}
                        >
                            <option value={0}>Inativo</option>
                            <option value={1}>Ativo</option>
                        </Select>
                    </FormControl>

                    <Grid container spacing={4}>
                        <Grid item>
                            <FormControl className={classes.formControl}>
                                <TextField
                                    className={classes.textField}
                                    value={props.values.date_start}
                                    label="Início da promoção" 
                                    type="date"
                                    onChange={e => props.setFieldValue('date_start', e.target.value)}
                                    InputLabelProps={{ shrink: true }}
                                />
                                {props.touched.date_start && props.errors.date_start && 
                                    <FormHelperText className={classes.error}>{props.errors.date_start}</FormHelperText>
                                }
                            </FormControl>
                        </Grid>
                        <Grid item>
                            <FormControl className={classes.formControl}>
                                <TextField
                                    className={classes.textField}
                                    value={props.values.date_end}
                                    label="Fim da promoção" 
                                    type="date"
                                    onChange={e => props.setFieldValue('date_end', e.target.value)}
                                    InputLabelProps={{ shrink: true }}
                                />
                                {props.touched.date_end && props.errors.date_end && 
                                    <FormHelperText className={classes.error}>{props.errors.date_end}</FormHelperText>
                                }
                            </FormControl>
                        </Grid>
                    </Grid>

                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            value={props.values.total_orders}
                            label="Total de Pedidos" 
                            name="total_orders"
                            type="number"
                            onChange={e => props.setFieldValue('total_orders', e.target.value)}
                        />
                        {props.touched.total_orders && props.errors.total_orders && 
                            <FormHelperText className={classes.error}>{props.errors.total_orders}</FormHelperText>
                        }
                    </FormControl>

                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            label="Descrição" 
                            name="description"
                            value={props.values.description}
                            rows={5}
                            multiline
                            onChange={e => props.setFieldValue('total_orders', e.target.value)}
                        />
                        {props.touched.description && props.errors.description && 
                            <FormHelperText className={classes.error}>{props.errors.description}</FormHelperText>
                        }
                    </FormControl>

                </Paper>

                <div className={classes.buttons}>
                    <LinkContainer to={'/app/credit-rule/list'}>
                        <Button variant="contained">
                            <ArrowBackIosIcon /> Voltar
                        </Button>
                    </LinkContainer>
                    <Button type="submit" variant="contained" color="primary">
                        Salvar <ArrowForwardIosIcon />
                    </Button>
                </div>
            </form>
        </div>
    );

}

export default withFormik({
    mapPropsToValues: props => ({ 
        id: '',
        name: '',
        description: '',
        observation: '',
        total_orders: '',
        active: false,
        date_start: '',
        date_end: '',
    }),

    validationSchema: Yup.object().shape({
      name: Yup.string()
        .required('Informe o nome da regra'),
      total_orders: Yup.string()
        .required('Informe a quantidade'),
    }),
  
    handleSubmit: async (values, { setSubmitting, setErrors, props, setStatus }) => {
        try {
            setSubmitting(true);

            let result = {};
            if (values.id) {
                result = await api.patch(`/creditRule/${values.id}`, values);
            } else {
                result = await api.post(`/creditRule`, values);
            }

          if (result.data.success) {
              setStatus({ success: true, data: values });
          }
  
          if (result.data.validations) {
              result.data.validations.map(e => {
                  let error = {};
                  error[e.field] = e.message;
                  setErrors(error);
              });
          }
  
        } catch(error) {
          console.log(error)
          setErrors({ error: 'Não foi possível salvar os dados. Tente novamente.' });
        }
    },
  
  })(CreditRuleForm);
  