import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

function rand() {
    return Math.round(Math.random() * 20) - 10;
}
  
function getModalStyle() {
    const top = 50 + rand();
    const left = 50 + rand();
  
    return {
      top: `${top}%`,
      left: `${left}%`,
      transform: `translate(-${top}%, -${left}%)`,
    };
}
  
const useStyles = makeStyles(theme => ({
    paper: {
      position: 'absolute',
      width: 400,
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(2, 4, 3),
      border: 'none !important',
    },
}));

export default function Details({ data, open, handleClose }) {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = useState(getModalStyle);

  if (!data.id) return <div />;

  return (
    <Modal
        open={open}
        onClose={handleClose}
        disablePortal
        disableEnforceFocus
        disableAutoFocus
      >
        <div style={modalStyle} className={classes.paper}>

            <h2>{data.product.name}</h2>
            {data.option && <h3>Opção: {data.option.name}</h3>}

            <h3>Opções Selecionadas</h3>

            {data.options.map((cartOption, i) => 
                <ListItem 
                    button
                    key={i}
                >
                    <ListItemText primary={cartOption.option.name} />
                </ListItem>
              )}

        </div>
      </Modal>
  );
}
