import React, { useState } from 'react';
import { Redirect, useParams } from 'react-router-dom';
import { useAlert } from "react-alert";
import Paper from '@material-ui/core/Paper';
import { CircularProgress } from '@material-ui/core';
import { useEffect } from 'react';

import Form from '../Form';
import PageTitle from "../../../components/PageTitle";
import useStyles from './styles';

import api from '../../../services/api';

export default () => {
  const [redirect, setRedirect] = useState(false);
  const [loading, setLoading] = useState(true);
  const [setting, setSetting] = useState({});

  const alert = useAlert();
  const classes = useStyles();

  const { id } = useParams();

  useEffect(() => {
    async function getData() {
      const { data } = await api.get(`/setting/${id}`)
      setSetting(data);
      setLoading(false);
    }

    getData();
  }, []);

  function onSuccess() { 
      alert.success('Salvo com sucesso!');
      return setRedirect(true);
  }

  if (redirect) return  <Redirect to={`/app/setting/list`} />;

  return (
    <div className={classes.root}>
      <PageTitle title="Editar Configuração" />

      <Paper className={classes.paper}>
        {loading && <CircularProgress size={24} className={classes.loading} />}

        {!loading && <Form setting={setting} onSuccess={onSuccess} />}
      </Paper>
    </div>
  );

}