import React, { useState, useEffect } from "react";
import { CircularProgress, Button, Grid } from '@material-ui/core';
import { useParams } from "react-router-dom";

import PageTitle from "../../../components/PageTitle";
import Loading from '../../../components/Loading';

import Products from './Products';
import ChangeStatus from './ChangeStatus';
import Customer from './Customer';
import OrderInfo from './OrderInfo';
import Address from './Address';

import api from '../../../services/api';

export default function() {
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(true);
  const [order, setOrder] = useState([]);
  const { id } = useParams();

  useEffect(() => {
    async function fetchData() {
      setLoading(true);
      const { data } = await api.get(`/order/${id}`);
      setOrder(data);
      setLoading(false);
    }

    fetchData();
  }, [id]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = (order_state) => {
    order.state.name = order_state.name;
    order.state.label = order_state.label;
    order.state.color = order_state.color;
    order.state.description = order_state.description;

    setOrder(prev => ({...order, ...order}));

    setOpen(false);
  };

  if (loading) return <Loading />

  return (
    <>
      <ChangeStatus open={open} handleClose={handleClose} order={order} />

      <PageTitle title="Vendas">
          <Button variant="contained" size="large" color="secondary" onClick={handleOpen}>
            Alterar Status
          </Button>
      </PageTitle>

      <div style={{ width: '100%' }}>
        <Grid container style={{ marginBottom: 50 }}>
          <Grid item>
            <Customer user={order.user} />
          </Grid>
          <Grid item style={{ marginLeft: 15 }} xs={8}>
            <OrderInfo order={order} />
          </Grid>
        </Grid>
      </div>

      <Address order={order} />

      <Products data={order.cart.products} />

      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

    </>
  );
}
