import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { LinkContainer } from 'react-router-bootstrap';
import { useAlert } from "react-alert";
import Chip from '@material-ui/core/Chip';
import CheckIcon from '@material-ui/icons/Check';
import DoneAllIcon from '@material-ui/icons/DoneAll';

import * as ReactIntl from 'react-intl';

import PageTitle from "../../../components/PageTitle";
import TableOptions from '../../../config/table';

import api from '../../../services/api';

const FormattedNumber = ReactIntl.FormattedNumber;

export default function() {
  const alert = useAlert();
  const [loading, setLoading] = useState(true);
  const [orders, setOrders] = useState([]);

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },    
    {  
      name: 'user.name', 
      label: 'Cliente',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'address.neighborhood.name', 
      label: 'Bairro',
      options: {
        filterType: 'textField'
      }
    },    
    {  
      name: 'state.label', 
      label: 'Status',
      options: {
        filterType: 'textField',
        customBodyRender: (value, props) => {
          const currentOrder = orders[props.rowIndex];
          return  <Chip size="small" label={value} style={{ background: currentOrder.state.color, color: '#fff' }} />
        }        
      }
    },
    {  
      name: 'total_price', 
      label: 'Valor Total',
      options: {
        filterType: 'textField',
        customBodyRender: (value) => {
          const Money = ({ value, style="currency" }) => (
            <FormattedNumber style={style} value={value} currency="BRL" />
          )
          return Money({ value });
        }        
      },
    },
    {  
      name: 'is_opened', 
      label: 'Visualizado',
      options: {
        filterType: 'textField',
        customBodyRender: (value) => {
          console.log(value)
          return <Chip 
                    label={value === 0 ? <CheckIcon /> : <DoneAllIcon />}
                  />
        }        
      },
    },    
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta) => {
          const selected = orders[tableMeta.rowIndex];
          return (
            <>
              <LinkContainer to={`/app/order/detail/${selected.id}`}>
                <Button>
                    <VisibilityIcon />
                </Button>
              </LinkContainer>
            </>
          );
        }
      }
    }    
  ];

  let itemsSelecteds = [];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: orders.length,
      onTableChange: (action, tableState) => {
        if (action === 'rowsSelect') {
          itemsSelecteds = [];

          tableState.selectedRows.data.map(data => {
            const selected = orders[data.index];
            itemsSelecteds.push(selected);
            return data;
          });
        }
      },
  };


  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/order?limit=20`);

      setOrders(data);
      setLoading(false);
    }

    fetchData();
  }, []);

  return (
    <>
      {!loading && 
        <MUIDataTable
          title="Últimas de Vendas"
          data={orders}
          columns={columns}
          options={options}
        />
      }
    </>
  );
}
