import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import EditIcon from '@material-ui/icons/Edit';
import { LinkContainer } from 'react-router-bootstrap';
import { useAlert } from "react-alert";
import Chip from '@material-ui/core/Chip';
import CheckIcon from '@material-ui/icons/Check';
import DoneAllIcon from '@material-ui/icons/DoneAll';

import PageTitle from "../../../components/PageTitle";
import TableOptions from '../../../config/table';

import api from '../../../services/api';

export default function() {
  const alert = useAlert();
  const [loading, setLoading] = useState(true);
  const [creditRules, setCreditRules] = useState([]);

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },    
    {  
      name: 'name', 
      label: 'Nome da Regra',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'total_orders', 
      label: 'Total de Pedidos',
      options: {
        filterType: 'textField',
      },
    },
      {  
      name: 'active', 
      label: 'Ativo',
      options: {
        filterType: 'textField',
        customBodyRender: (value) => {
          return <Chip label={value !== 1 ? <CheckIcon /> : <DoneAllIcon />} />
        }        
      },
    }, 
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta) => {
          const selected = creditRules[tableMeta.rowIndex];
          return (
            <>
              <LinkContainer to={`/app/credit-rule/edit/${selected.id}`}>
                <Button>
                    <EditIcon />
                </Button>
              </LinkContainer>
              <Button onClick={async () => {
                if (window.confirm('Remover esse item?')) {
                  await deleteItem(selected.id);
                  creditRules.splice(tableMeta.rowIndex, 1);
                  setCreditRules(creditRules.slice());
                  alert.success('Item excluido com sucesso!');
                }
              }}>
                <HighlightOffIcon />
              </Button>
            </>
          );
        }
      }
    }    
  ];

  let itemsSelecteds = [];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: creditRules.length,
      onTableChange: (action, tableState) => {
        if (action === 'rowsSelect') {
          itemsSelecteds = [];

          tableState.selectedRows.data.map(data => {
            const selected = creditRules[data.index];
            itemsSelecteds.push(selected);
            return data;
          });
        }

        if (action === 'rowDelete') {
          itemsSelecteds.map(order => {
            deleteItem(order.id);
            alert.success('Registros selecionados excluidos com sucesso!');
            return null;
          });
          
        }
      },
  };

  const deleteItem = async (id) => {
    await api.delete(`/creditRule/${id}`);
  }

  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/creditRule`);

      setCreditRules(data);
      setLoading(false);
    }

    fetchData();
  }, []);

  return (
    <>
      <PageTitle title="Regras de créditos">
          <LinkContainer to="/app/credit-rule/create">
            <Button variant="contained" size="large" color="secondary">
              Cadastrar
            </Button>
          </LinkContainer>
      </PageTitle>

      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

      {!loading && 
        <MUIDataTable
          title="Regras de créditos"
          data={creditRules}
          columns={columns}
          options={options}
        />
      }
    </>
  );
}
