import React, { useState, useEffect } from 'react';
import { useAlert } from "react-alert";
import { LinkContainer } from 'react-router-bootstrap';
import { Redirect, useParams } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import objectToFormData from 'object-to-formdata';
import { TextField, Button, FormControl } from '@material-ui/core';  
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import PageTitle from "../../../components/PageTitle";
import Loading from '../../../components/Loading';
import useStyles from './styles';

import api from '../../../services/api';

export default () => {
  const alert = useAlert();
  // const [loading, setLoading] = useState(false);
  const [loadingData, setLoadingData] = useState(true);
  const [ orderState, setOrderState] = useState({});
  const [redirect, setRedirect] = useState(false);
  const { id } = useParams();
  const classes = useStyles();


  useEffect(() => {
    async function fetchData() {
      const { data } = await api.get(`/orderState/${id}`);
      setOrderState(data);
      setLoadingData(false);
    }
    
    fetchData();
  }, [id])
  
  const handleOnSubmit = async () => { 
    try {
      const options = { indices: true, nullsAsUndefineds: false };
      const formData = objectToFormData(orderState, options);
      await api.patch(`/orderState/${id}`, formData);
      
      alert.success('Salvo com sucesso!');
      setRedirect(true);
    } catch (error) {
      alert.error(error.message);
    }
  }
  
  if (redirect) return  <Redirect to={`/app/order-state/list`} />;

  if (loadingData) return <Loading />;

  return (
    <div className={classes.root}>
      
      <PageTitle title='Editar Status' />

        <form onSubmit={handleOnSubmit}>
          <Paper className={classes.paper}>
              <FormControl style={{width: '100%'}}>
                <TextField 
                    className={classes.textField}
                    label="Informe o nome do status" 
                    name="name"
                    onChange={e => orderState.name = e.target.value}
                    defaultValue={orderState.name}
                />
              </FormControl>
              <FormControl style={{width: '100%'}}>
                <TextField 
                    className={classes.textField}
                    label="Label" 
                    name="name"
                    onChange={e => orderState.label = e.target.value}
                    defaultValue={orderState.label}
                />
              </FormControl>
              <FormControl style={{width: '100%'}}>
                <TextField 
                    className={classes.textField}
                    label="Cor" 
                    name="color"
                    onChange={e => orderState.color = e.target.value}
                    defaultValue={orderState.color}
                    type="color"
                    style={{width: 70, textAlign: 'left'}}
                />
              </FormControl>                            
              <FormControl style={{width: '100%'}}>
                <TextField 
                    className={classes.textField}
                    multiline
                    rows="4"
                    label="Descrição" 
                    onChange={e => orderState.description = e.target.value}
                    defaultValue={orderState.description}
                />
              </FormControl>
          </Paper>

          <div className={classes.buttons}>
            <LinkContainer to={'/app/order-state/list'}>
              <Button  variant="contained">
                <ArrowBackIosIcon /> Voltar
              </Button>
            </LinkContainer>
            <Button type="button" onClick={() => handleOnSubmit()} variant="contained" color="primary">
              Salvar <ArrowForwardIosIcon />
            </Button>
          </div>
        </form>
    </div>
  );

}