import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import { ListSubheader } from '@material-ui/core';

import api from '../../../services/api';
import { useAlert } from 'react-alert';

function rand() {
    return Math.round(Math.random() * 20) - 10;
}
  
function getModalStyle() {
    const top = 50 + rand();
    const left = 50 + rand();
  
    return {
      top: `${top}%`,
      left: `${left}%`,
      transform: `translate(-${top}%, -${left}%)`,
    };
}
  
const useStyles = makeStyles(theme => ({
    paper: {
      position: 'absolute',
      width: 400,
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(2, 4, 3),
      border: 'none !important',
    },
}));

export default function Details({ order, open, handleClose }) {
  const [orderStates, setOrderStates] = useState([]);
  const alert = useAlert();
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);

  useEffect(() => {
    async function getOrderStatus() {
      const { data } = await api.get('/orderState');
      setOrderStates(data);
      // setLoading(false);
    }

    getOrderStatus();
  }, [])

  async function onSave(order_state) {
    await api.patch(`/order/${order.id}`, {order_state_id: order_state.id});
    alert.show('Status alterado com sucesso!');
    handleClose(order_state);
  } 

  if (orderStates.length <= 0) return <div />;

  return (
    <Modal
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        open={open}
        onClose={handleClose}
        disablePortal
        disableEnforceFocus
        disableAutoFocus
      >
        <div style={modalStyle} className={classes.paper}>

        <List
            component="nav"
            aria-labelledby="nested-list-subheader"
            subheader={
              <ListSubheader component="div" id="nested-list-subheader">
                 Selecione o Status
              </ListSubheader>
            }
            className={classes.root}
          >
            
            {orderStates.map((orderState, i) => 
                <ListItem key={i} button onClick={() => {
                  onSave(orderState);
                }}>
                    <ListItemText primary={orderState.name} />
                    <ChevronRightIcon />
                </ListItem>
              )}
          </List>
        </div>
      </Modal>
  );
}
