import React, { useState, useEffect } from 'react';
import { Redirect, useParams } from 'react-router-dom';
import { useAlert } from "react-alert";
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import CircularProgress from '@material-ui/core/CircularProgress';
import SaveIcon from '@material-ui/icons/Save';
import objectToFormData from 'object-to-formdata';
import MenuItem from '@material-ui/core/MenuItem';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Chip from '@material-ui/core/Chip';

import PageTitle from "../../../components/PageTitle";
import useStyles from './styles';
import AddOptions from '../AddOptions';
import Images from '../Images';
import Options from '../Options';
import api from '../../../services/api';

export default () => {
  const alert = useAlert();
  const [listCategories, setCategories] = useState('');
  const [product, setProduct] = useState({});
  const [loading, setLoading] = useState(false);
  const [redirect, setRedirect] = useState(false);

  const classes = useStyles();
  
  const { id } = useParams();

  useEffect(() => {
    async function getCategories() {
      const { data } = await api.get('/category/all');
      setCategories(data);
    }

    getCategories();
  }, []);

  useEffect(() => {
    async function getProduct() {
      const { data } = await api.get(`/product/${id}`);
      setProduct(data);
    }

    getProduct();
  }, [id]);
  
  const onSubmit = async () => { 
    try {
        setLoading(true);
        const options = { indices: true, nullsAsUndefineds: false };
        const formData = objectToFormData(product, options);
        await api.patch(`/product/${id}`, formData);

        alert.success('Salvo com sucesso!');
        setLoading(false);
        setRedirect(true);
    } catch (error) {
      alert.error(error.message);
    }

    return false;
  }

  if (!product.id) return '...';

  return (
    <div className={classes.root}>
      
      {redirect && <Redirect to={`/app/product/list`} />}

      <PageTitle title="Editar Produto" />

      <form onSubmit={onSubmit}> 
        <Paper className={classes.paper}>

            <FormControl className={classes.formControl}>
                <InputLabel>Status</InputLabel>
                <Select
                    value={product.active}
                    onChange={e => {
                      product.active = e.target.value;
                      setProduct({...product, product});
                    }}
                    style={{  textAlign: 'left' }}
                >
                        <MenuItem value={1}>Ativo</MenuItem>
                        <MenuItem value={0}>Inativo</MenuItem>
                </Select>                       
            </FormControl>

            <FormControl className={classes.formControl}>
              <TextField 
                  className={classes.textField}
                  label="Informe o nome do Produto" 
                  defaultValue={product.name}
                  name="name"
                  onChange={(e) => product.name = e.target.value}
              />
            </FormControl>
            <FormControl className={classes.formControl}>
              <TextField 
                  className={classes.textField}
                  label="Preço do Produto" 
                  defaultValue={product.price}
                  name="price"
                  onChange={(e) => product.price = e.target.value}
              />
            </FormControl>
            <FormControl className={classes.formControl}>
              <TextField 
                  className={classes.textField}
                  defaultValue={product.short_description}
                  name="short_description"
                  multiline
                  rows="4"
                  label="Breve Descrição" 
                  onChange={(e) => product.short_description = e.target.value}
              />
            </FormControl>

            <FormControl className={classes.formControl}>
              <TextField 
                  className={classes.textField}
                  defaultValue={product.description}
                  name="description"
                  multiline
                  rows="4"
                  label="Descrição" 
                  onChange={(e) => product.description = e.target.value}
              />
            </FormControl>

            {listCategories.length > 0 && <FormControl className={classes.formControl}>
              <InputLabel>Categorias Relacionadas</InputLabel>
              <Select
                multiple
                defaultValue={product.categories}
                onChange={(e) => {
                    product.categories = e.target.value;
                }}
                input={<Input />}
                renderValue={selected => (
                  <div className={classes.chips}>
                    {selected.map(value => (
                        <Chip key={value} label={listCategories.find(i => i.id === value).name} className={classes.chip} />
                    ))}
                  </div>
                )}
              >
                {listCategories.map(category => (
                  <MenuItem key={category.id} value={category.id} >
                    {category.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            }

            <Grid container>
              <Grid item xs={3}>
                <img src={product.image_url} alt={product.image_url} style={{ maxWidth: '100%' }} />
              </Grid>
              <Grid item>
                <TextField 
                    className={classes.textField}
                    label="Imagem Destaque" 
                    name="image"
                    type="file"
                    onChange={(e) => product.image = e.target.files[0]}
                    InputLabelProps={{
                      shrink: true,
                    }}                      
                />                 
              </Grid>
            </Grid>              
        </Paper>

        <Options product={product} />

        {/* List Group and Options */}
        <AddOptions title="Adicionais do Produto" />
        <hr />

        <Images product={product} />

        <div className={classes.buttons} style={{ marginTop: 50 }}>
          <Button  variant="contained">
            <ArrowBackIosIcon /> Voltar
          </Button>
          <Button type="button" onClick={() => onSubmit()} variant="contained" color="primary" disabled={loading}>
            {loading ? <CircularProgress color="inherit" style={{ marginRight: 10, fontSize: 15 }} /> : <SaveIcon />}
            Salvar
          </Button>
        </div>
      </form>
    </div>
  );

}
