import React from 'react';
import { useAlert } from "react-alert";
import 'react-dropzone-uploader/dist/styles.css';
import Dropzone from 'react-dropzone-uploader';

import { url } from '../../../config';

export default function ImageAudioVideo({ product, handleClose }) {
  const alert = useAlert();

  const getUploadParams = async ({ file }) => {
    const body = new FormData();
    body.append('image', file);
    return { url: `${url}/product/${product.id}/images`, body };
  }

  const handleChangeStatus = ({ meta }, status) => {
    // console.log(status, meta)
  }

  const handleSubmit = (files, allFiles) => {
    allFiles.forEach(f => f.remove());
    handleClose();
    alert.success('Salvo com sucesso!');
  }

  return (
      <Dropzone
        getUploadParams={getUploadParams}
        onChangeStatus={handleChangeStatus}
        onSubmit={handleSubmit}
        accept="image/*"
        canRemove={false}
        styles={{
          dropzoneReject: { borderColor: 'red', backgroundColor: '#DAA' },
          inputLabel: (files, extra) => (extra.reject ? { color: 'red' } : {}),
        }}
        inputContent={(files, extra) => (extra.reject ? 'Apenas arquivos de imagem' : 'Arraste arquivos ou clique para procurar')}
        submitButtonContent={'Finalizar'}
        inputWithFilesContent={'Adicionar Imagens'}        
      />
  )
}