import React from 'react';
import { withFormik } from 'formik';
import * as Yup from 'yup';
import { FormControl, TextField, Button } from '@material-ui/core';
import { Editor } from '@tinymce/tinymce-react';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import CircularProgress from '@material-ui/core/CircularProgress';
import { LinkContainer } from 'react-router-bootstrap';
import objectToFormData from 'object-to-formdata';
import SaveIcon from '@material-ui/icons/Save';

import api from '../../../services/api';

import useStyles from './styles';

function Form(props) {
    const classes = useStyles();

    return (
      <form onSubmit={props.handleSubmit}>
          <FormControl className={classes.formControl}>
            <TextField 
                label="Título da página" 
                onChange={e => props.setFieldValue('title', e.target.value)}
                value={props.values.title}
                name="title"
            />
            {props.touched.title && props.errors.title && 
              <div className={classes.textError}>{props.errors.title}</div>
            }
          </FormControl>

          <FormControl className={classes.formControl}>
            <Editor
              init={{ plugins: 'link table' }}
              value={props.values.content}
              onEditorChange ={content => props.setFieldValue('content', content)}
            />
          </FormControl>
          
          <div className={classes.buttons}>
            <LinkContainer to={'/app/page/list'}>
              <Button variant="contained">
                <ArrowBackIosIcon /> Voltar
              </Button>
            </LinkContainer>
            <Button 
              type="button" 
              onClick={props.handleSubmit} 
              variant="contained" 
              color="primary"
              disabled={props.isSubmitting}
            >
              Salvar 
              {!props.isSubmitting && <SaveIcon />}
              {props.isSubmitting && <CircularProgress className={classes.spinner} />}
            </Button>
          </div>          
      </form>
    )
}

export default withFormik({
  mapPropsToValues: props => ({ 
      id: props.page ? props.page.id : '',
      title: props.page ? props.page.title : '',
      content: props.page ? props.page.content : '',
    }),

  validationSchema: Yup.object().shape({
      title: 
        Yup.string()
        .required('Informe o título da página')
  }),

  handleSubmit: async (values, { setSubmitting, props }) => {
      setSubmitting(true);
      
      const options = { indices: true, nullsAsUndefineds: false };
      const formData = objectToFormData(values, options);

      if (!values.id) {
        const { data } = await api.post('/page', formData);
        setSubmitting(false);
        return props.onSuccess(data);
      }

      await api.patch(`/page/${values.id}`, formData);
      props.onSuccess(values);

      setSubmitting(false);
  },

})(Form);

