import React, { useState, useEffect } from 'react';
import { Redirect, useParams } from 'react-router-dom';
import { useAlert } from "react-alert";
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import SaveIcon from '@material-ui/icons/Save';
import CircularProgress from '@material-ui/core/CircularProgress';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import objectToFormData from 'object-to-formdata';
import { LinkContainer } from 'react-router-bootstrap';

import PageTitle from "../../../components/PageTitle";
import useStyles from './styles';
import Loading from '../../../components/Loading';

import api from '../../../services/api';
import errors from '../../../utils/errors';

export default () => {
  const alert = useAlert();
  const [redirect, setRedirect] = useState(false);
  const [banner, setBanner] = useState({});
  const [listErrors, setListErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [loadingData, setLoadingData] = useState(true);

  const classes = useStyles();
  const { id } = useParams();

  useEffect(() => {
    async function fetchData() {
      const { data } = await api.get(`/banner/${id}`);
      setBanner(data);
      setLoadingData(false);
    }

    fetchData();
  }, [id])

  if (loadingData) return <Loading />

  const onSubmit = async () => { 
    try {
        if (!banner.image) {
          return setListErrors({ image: 'Selecione uma imagem' });
        }

        setLoading(true);
        setListErrors([]);

        const options = { indices: true, nullsAsUndefineds: false };
        const formData = objectToFormData(banner, options);
        const { data } = await api.patch(`/banner/${banner.id}`, formData);
        setLoading(false);

        if (!data.success) {
          if (data.errors.length > 0) {
            return setListErrors(errors(data.errors));
          }
        }

        alert.success('Salvo com sucesso!');

        setRedirect(true);
    } catch (error) {
      console.log( error )
    }

    return false;
  }

  return (
    <div className={classes.root}>
      
        {redirect && <Redirect to={`/app/banner/list`} />}

        <PageTitle title="Cadastrar Banner" />

        <form onSubmit={onSubmit}>
          <Paper className={classes.paper}>

            <FormControl className={classes.formControl}>
              <InputLabel>Bloco</InputLabel>
              <Select
                name="hook"
                defaultValue={banner.hook}
                onChange={e => banner.hook = e.target.value }
                className={classes.select}
              >
                <MenuItem value={'slideshow'}>Slideshow</MenuItem>
              </Select>
              {listErrors.hook && <FormHelperText className={classes.error}>{listErrors.hook}</FormHelperText>}
            </FormControl>

            <FormControl className={classes.formControl}>     
              <TextField 
                  className={classes.textField}
                  label="Nome" 
                  name="name"
                  defaultValue={banner.name}
                  onChange={e => banner.name = e.target.value}
              />
              {listErrors.name && <FormHelperText className={classes.error}>{listErrors.name}</FormHelperText>}
            </FormControl>

            <FormControl className={classes.formControl}>
              <TextField 
                  className={classes.textField}
                  multiline
                  rows="3"
                  label="Descrição" 
                  defaultValue={banner.description}
                  onChange={e => banner.description = e.target.value}
              />
            </FormControl>

            <Grid container spacing={2}>
              <Grid item xs={3}>
                  <FormControl className={classes.formControl}>
                    <InputLabel>Abertura de Link</InputLabel>
                    <Select
                      defaultValue={banner.link_target}
                      onChange={e => banner.link_target = e.target.value }
                      className={classes.select}
                    >
                      <MenuItem value={'_blank'}>Nova Guia</MenuItem>
                      <MenuItem value={'_self'}>Mesma Guia</MenuItem>
                    </Select>
                    {listErrors.link_target && <FormHelperText className={classes.error}>{listErrors.link_target}</FormHelperText>}
                </FormControl>                
              </Grid>

              <Grid item xs={9}>
                <FormControl className={classes.formControl}>     
                  <TextField 
                      className={classes.textField}
                      label="Link" 
                      name="link"
                      defaultValue={banner.link}
                      onChange={e => banner.link = e.target.value}
                  />
                  {listErrors.link && <FormHelperText className={classes.error}>{listErrors.link}</FormHelperText>}
                </FormControl>
              </Grid>
            </Grid>

            <FormControl className={classes.formControl}>
              <InputLabel>Imagem</InputLabel>
              <TextField 
                  className={classes.textField}
                  label="Imagem" 
                  type="file"
                  onChange={e => banner.image = e.target.files[0]}
                  InputLabelProps={{
                    shrink: true,
                  }}                  
              />
              {listErrors.image && <FormHelperText className={classes.error}>{listErrors.image}</FormHelperText>}
            </FormControl>

          </Paper>

          <Paper className={classes.paper}>
            <img src={banner.image_url} alt={banner.image} className={classes.image} />
          </Paper>

          <div className={classes.buttons}>
            <LinkContainer to={'/app/banner/list'}>
              <Button  variant="contained">
                <ArrowBackIosIcon /> Voltar
              </Button>
            </LinkContainer>
            <Button type="button" onClick={() => onSubmit()} variant="contained" color="primary" disabled={loading}>
               {loading ? <CircularProgress color="inherit" style={{ marginRight: 10 }} /> : <SaveIcon />}
                Salvar
            </Button>
          </div>
        </form>
    </div>
  );

}