import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useAlert } from "react-alert";
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardActions from '@material-ui/core/CardActions';
import DeleteIcon from '@material-ui/icons/Delete';

import api from '../../../services/api';

const useStyles = makeStyles(theme => ({
  card: {
    maxWidth: 345,
    marginBottom: 15
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
}));

export default function({ product, open }) {
  const alert = useAlert();
  const classes = useStyles();
  const [images, setImages] = React.useState([]);

  useEffect(() => {
    async function getImages() {
      const { data } = await api.get(`/product/${product.id}/images`);
      setImages(data);
    }

    getImages();
  }, [open, product]);

  const deleteImage = async (image, i) => {
    await api.delete(`/product/${product.id}/images/${image.id}`);
    images.splice(i, 1);
    setImages(images.slice());
    alert.success('Item excluido!');
  }

  return (
    <Grid container spacing={2}>
      {images.map((image, i) =>
        <Grid item md={3} xs={2} key={i}>
          <Card className={classes.card}>
            <CardMedia className={classes.media} image={image.url} title="" />
            <CardActions disableSpacing>
              <Button onClick={() => { deleteImage(image, i); }}>
                <DeleteIcon />
              </Button>
            </CardActions>
          </Card>
        </Grid>
      )}
    </Grid>
  );
}