import React, { useState, useEffect } from 'react';
import { withFormik } from 'formik';
import * as Yup from 'yup';
import { LinkContainer } from 'react-router-bootstrap';
import { Button, Paper, FormControl, TextField, FormHelperText, Grid, InputLabel, Select, MenuItem, Input, Chip } from '@material-ui/core';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { Redirect } from 'react-router-dom';
import { useAlert } from "react-alert";

import api from '../../../services/api';
import useStyles from './styles';

function RegisterUser(props) {
    const classes = useStyles();
    const [roles, setRoles] = useState([]);
    const [redirect, setRedirect] = useState(false);
    const alert = useAlert();
   
    useEffect(() => {
        if (props.status && props.status.success) {
            alert.show('Salvo com sucesso!');
            setRedirect(true);
        }
    }, [props.status]);
    
    useEffect(() => {
        async function fetchRoles() {
            const { data } = await api.get(`/role/all`);
            setRoles(data);
        }

        fetchRoles();

        if (props.user) {
            props.setFieldValue('id', props.user.id);
            props.setFieldValue('name', props.user.name);
            props.setFieldValue('email', props.user.email);
            props.setFieldValue('phone', props.user.phone || '');
            props.setFieldValue('person_type', props.user.person_type);
            props.setFieldValue('document', props.user.document);
            props.setFieldValue('role_ids', props.user.role_ids);
        }
    }, [])

    if (!roles.lenght <= 0) return <div />;

    if (redirect) return<Redirect to={`/app/user/list`} />

    return (
        <form onSubmit={props.handleSubmit}>
           <Paper className={classes.paper}>

                <FormControl className={classes.formControl}>
                    <TextField
                        className={classes.textField}
                        label="Nome Completo" 
                        value={props.values.name}
                        onChange={e => props.setFieldValue('name', e.target.value)}
                    />
                    {props.touched.name && props.errors.name &&  
                        <FormHelperText className={classes.error}>{props.errors.name}</FormHelperText>
                    }
               </FormControl>

               <FormControl className={classes.formControl}>
                    <TextField
                        type="email"
                        className={classes.textField}
                        label="E-mail" 
                        value={props.values.email}
                        onChange={e => props.setFieldValue('email', e.target.value)}
                    />
                    {props.touched.email && props.errors.email &&  
                        <FormHelperText className={classes.error}>{props.errors.email}</FormHelperText>
                    }
               </FormControl>

               <FormControl className={classes.formControl}>
                    <TextField
                        className={classes.textField}
                        label="Celular" 
                        value={props.values.phone}
                        onChange={e => props.setFieldValue('phone', e.target.value)}
                    />
                    {props.touched.phone && props.errors.phone &&  
                        <FormHelperText className={classes.error}>{props.errors.phone}</FormHelperText>
                    }
               </FormControl>

               <Grid container spacing={2}>
                 <Grid item xs={4}>
                   <FormControl className={classes.formControl}>
                       <InputLabel id="user-person-type">Tipo de Pessoa</InputLabel>
                       <Select
                           value={props.values.person_type}
                           onChange={e => props.setFieldValue('person_type', e.target.value)}
                       >
                         <MenuItem value={'individual'}>Pessoa Física</MenuItem>
                         <MenuItem value={'legal'}>Pessoa Jurídica</MenuItem>
                       </Select>
                   </FormControl>
                 </Grid>

                 <Grid item xs={8}>
                   <FormControl className={classes.formControl}>
                     <TextField 
                         className={classes.textField}
                         value={props.values.document}
                         onChange={e => props.setFieldValue('document', e.target.value)}
                         label={props.values.person_type === 'legal' ? 'CNPJ' : 'CPF'}
                     />
                    {props.touched.document && props.errors.document &&  
                        <FormHelperText className={classes.error}>{props.errors.document}</FormHelperText>
                    }                     
                   </FormControl>
                 </Grid>
               </Grid>

               {roles.length > 0 && <FormControl className={classes.formControl}>
                 <InputLabel>Regras de Acesso</InputLabel>
                 <Select
                   multiple
                   value={props.values.role_ids}
                   onChange={e => props.setFieldValue('role_ids', e.target.value)}
                   input={<Input />}
                   renderValue={selected => (
                     <div className={classes.chips}>
                       {selected.map(value => (
                           <Chip key={value} label={roles.find(i => i.id === value).name} className={classes.chip} />
                       ))}
                     </div>
                   )}
                 >
                   {roles.map(role => (
                     <MenuItem key={role.id} value={role.id} >
                       {role.name}
                     </MenuItem>
                   ))}
                 </Select>

                {props.touched.role_ids && props.errors.role_ids &&  
                    <FormHelperText className={classes.error}>{props.errors.role_ids}</FormHelperText>
                }                  
                </FormControl>
                }

                <FormControl className={classes.formControl}>
                    <TextField
                        className={classes.textField}
                        label="Senha" 
                        value={props.values.password}
                        onChange={e => props.setFieldValue('password', e.target.value)}
                    />
                    {props.touched.password && props.errors.password &&  
                        <FormHelperText className={classes.error}>{props.errors.password}</FormHelperText>
                    }
               </FormControl>

            </Paper>

            <div className={classes.buttons}>
               <LinkContainer to={'/app/user/list'}>
                   <Button variant="contained">
                     <ArrowBackIosIcon /> Voltar
                   </Button>
               </LinkContainer>
               <Button type="submit" variant="contained" color="primary">
                 Salvar <ArrowForwardIosIcon />
               </Button>
            </div>

        </form>
    )

}

export default withFormik({
    mapPropsToValues: props => ({ 
        id: '',
        name: '',
        email: '',
        password: '',
        person_type: 'individual',
        document: '',
        phone: '',
        role_ids: []
    }),
  
    validationSchema: Yup.object().shape({
      role_ids: Yup.array().required('Selecione as permissões'),
      name: Yup.string()
        .required('Informe seu nome')
        .test('real', 'Informe seu nome completo.', (value) => {
            if (!value) return;
  
            const count = value.split(' ');
  
            if (!count[1]) {
                return false;
            }
  
            return true;
        }),      
      email: Yup.string().email('Informe um e-mail válido')
          .required('Informe seu e-mail'),
      phone: Yup.string().required('Esse campo é obrigatório'),
      password: Yup.string()
              .min(4, 'Digite uma senha com mais de 4 caracteres'),
      document: Yup.string().required('Esse campo é obrigatório')
    }),
  
    handleSubmit: async (values, { setSubmitting, setErrors, props, setStatus }) => {
        try {
          setSubmitting(true);

          if (!values.id && !values.password) {
            return setErrors({ password: 'Digite uma senha com mais de 4 caracteres' });
          }

          let result;

          if (values.id) {
            result = await api.patch(`/user/${values.id}`, values);
          } else {
            result = await api.post(`/user`, values);
          }
  
          if (result.data.success) {
              setStatus({ success: true, data: result });
          }

          if (result.data.validations) {
              result.data.validations.map(e => {
                  let error = {};
                  error[e.field] = e.message;
                  setErrors(error);
              });
          }
  
        } catch(error) {
            console.log(error);
            setErrors({ error: 'Não foi possível cadastrar esse registro. Tente Novamente.' });
        }
    },
  
  })(RegisterUser);
  
  