import React, { useState } from "react";
import { Grid, CircularProgress, Typography, Button, TextField, Fade } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import { useDispatch } from 'react-redux';

import { setToken, setUser } from  '../../store/actions/auth';

import api from '../../services/api';

// styles
import useStyles from "./styles";

function Login(props) {
  const classes = useStyles();
  const dispatch = useDispatch();

  // local
  const [isLoading, setIsLoading] = useState(false);
  const [error] = useState(null);
  const [loginValue, setLoginValue] = useState("cleomarocampos@gmail.com");
  const [passwordValue, setPasswordValue] = useState("cleomar201");


  async function onLogin() {
    setIsLoading(true);

    const { data } = await api.post('/auth/login', {
      email: loginValue,
      password: passwordValue
    });

    localStorage.setItem('token', data.token);

    dispatch(setToken(data.token));
    dispatch(setUser(data.data));
  }

  return (
    <Grid container className={classes.container}>
      <div className={classes.logotypeContainer}>
        {/* <img src={logo} alt="logo" className={classes.logotypeImage} /> */}
        <Typography className={classes.logotypeText}>Monka</Typography>
      </div>
      <div className={classes.formContainer}>
        <div className={classes.form}>

            <React.Fragment>
              <Typography variant="h4" className={classes.greeting}>
                Bem Vindo
              </Typography>
              
              <div className={classes.formDividerContainer}>
                <div className={classes.formDivider} />
              </div>

              <Fade in={error}>
                <Typography color="secondary" className={classes.errorMessage}>
                  Something is wrong with your login or password :(
                </Typography>
              </Fade>
              <TextField
                id="email"
                InputProps={{ 
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={loginValue}
                onChange={e => setLoginValue(e.target.value)}
                margin="normal"
                placeholder="E-mail"
                type="email"
                fullWidth
              />
              <TextField
                id="password"
                InputProps={{
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={passwordValue}
                onChange={e => setPasswordValue(e.target.value)}
                margin="normal"
                placeholder="Senha"
                type="password"
                fullWidth
              />

              <div className={classes.formButtons}>
                {isLoading ? (
                  <CircularProgress size={26} className={classes.loginLoader} />
                ) : (
                  <Button
                    disabled={
                      loginValue.length === 0 || passwordValue.length === 0
                    }
                    onClick={onLogin}
                    variant="contained"
                    color="primary"
                    size="large"
                  >
                    Entrar
                  </Button>
                )}
                <Button
                  color="primary"
                  size="large"
                  className={classes.forgetButton}
                >
                  Recuperar Senha
                </Button>
              </div>
            </React.Fragment>
        </div>

        <Typography color="primary" className={classes.copyright}>
          © Monka - Todos os Direitos Reservados
        </Typography>
      </div>
    </Grid>
  );
}

export default withRouter(Login);
