import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import EditIcon from '@material-ui/icons/Edit';
import { LinkContainer } from 'react-router-bootstrap';
import { useAlert } from "react-alert";
import moment from 'moment';

import * as ReactIntl from 'react-intl';

import PageTitle from "../../../components/PageTitle";
import TableOptions from '../../../config/table';

import api from '../../../services/api';

const FormattedNumber = ReactIntl.FormattedNumber;

export default function() {
  const alert = useAlert();
  const [loading, setLoading] = useState(true);
  const [vouchers, setVouchers] = useState([]);

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },    
    { name: 'name', label: 'Cupom' },
    { name: 'date_start', label: 'Início',
      options: {
        customBodyRender: (value) => {
          return moment(value).format('DD/MM/YYYY');
        }
      }
    },
    { name: 'date_end', label: 'Encerramento',
      options: {
        customBodyRender: (value) => {
          return moment(value).format('DD/MM/YYYY');
        }
      }
    },
    {  
      name: 'discount_value', 
      label: 'Desconto',
      options: {
        filterType: 'textField',
        customBodyRender: (value, props) => {
            const voucher = vouchers[props.rowIndex];
            if (voucher.discount_type === "money") {
              return <FormattedNumber style="currency" value={value} currency="BRL" />
            }
          
            return `% ${value}`;
        }        
      },
    },    
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta) => {
          const selected = vouchers[tableMeta.rowIndex];
          return (
            <>
              <LinkContainer to={`/app/voucher/edit/${selected.id}`}>
                <Button>
                    <EditIcon />
                </Button>
              </LinkContainer>
              <Button onClick={async () => {
                if (window.confirm('Remover esse item?')) {
                  await deleteItem(selected.id);
                  vouchers.splice(tableMeta.rowIndex, 1);
                  setVouchers(vouchers.slice());
                  alert.success('Item excluido com sucesso!');
                }
              }}>
                <HighlightOffIcon />
              </Button>              
            </>
          );
        }
      }
    }    
  ];

  let itemsSelecteds = [];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: vouchers.length,
      onTableChange: (action, tableState) => {
        if (action === 'rowsSelect') {
          itemsSelecteds = [];

          tableState.selectedRows.data.map(data => {
            const selected = vouchers[data.index];
            itemsSelecteds.push(selected);
            return data;
          });
        }

        if (action === 'rowDelete') {
          itemsSelecteds.map(order => {
            deleteItem(order.id);
            alert.success('Registros selecionados excluidos com sucesso!');
            return null;
          });
          
        }
      },
  };

  const deleteItem = async (id) => {
    await api.delete(`/voucher/${id}`);
  }

  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/voucher`);

      setVouchers(data);
      setLoading(false);
    }

    fetchData();
  }, []);

  return (
    <>
      <PageTitle title="Vouchers">
          <LinkContainer to="/app/voucher/create">
            <Button variant="contained" size="large" color="secondary">
              Cadastrar
            </Button>
          </LinkContainer>
      </PageTitle>

      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

      {!loading && 
        <MUIDataTable
          title="Lista de Vouchers"
          data={vouchers}
          columns={columns}
          options={options}
        />
      }
    </>
  );
}
