import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import { useAlert } from "react-alert";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import objectToFormData from 'object-to-formdata';
import { LinkContainer } from 'react-router-bootstrap';

import PageTitle from "../../../components/PageTitle";
import useStyles from './styles';

import api from '../../../services/api';

export default () => {
  const alert = useAlert();
  // const [loading, setLoading] = useState(false);
  const [redirect, setRedirect] = useState(false);
  const [orderState] = useState({});

  const classes = useStyles();

  const handleOnSubmit = async () => { 
    try {
        const options = { indices: true, nullsAsUndefineds: false };
        const formData = objectToFormData(orderState, options);
        await api.post('/orderState', formData);
        
        alert.success('Salvo com sucesso!');
        setRedirect(true);
    } catch (error) {
      console.log( error )
    }
  }

  if (redirect) return  <Redirect to={`/app/order-state/list`} />;

  return (
    <div className={classes.root}>
      
      <PageTitle title="Cadastrar Status" />

        <form onSubmit={handleOnSubmit}>
          <Paper className={classes.paper}>
              <TextField 
                  className={classes.textField}
                  label="Informe o nome do status" 
                  name="name"
                  onChange={e => orderState.name = e.target.value}
              />
              <TextField 
                  className={classes.textField}
                  label="Label" 
                  name="name"
                  onChange={e => orderState.label = e.target.value}
              />
              <TextField 
                  className={classes.textField}
                  multiline
                  rows="4"
                  label="Descrição" 
                  onChange={e => orderState.description = e.target.value}
              />
          </Paper>

          <div className={classes.buttons}>
            <LinkContainer to={'/app/order-state/list'}>
              <Button  variant="contained">
                <ArrowBackIosIcon /> Voltar
              </Button>
            </LinkContainer>
            <Button type="button" onClick={() => handleOnSubmit()} variant="contained" color="primary">
              Cadastrar <ArrowForwardIosIcon />
            </Button>
          </div>
        </form>
    </div>
  );

}