import React, { useState } from "react";
import MUIDataTable from "mui-datatables";
import { Button } from '@material-ui/core';
import VisibilityIcon from '@material-ui/icons/Visibility';
import * as ReactIntl from 'react-intl';

import PageTitle from "../../../../components/PageTitle";
import TableOptions from '../../../../config/table';
import Details from './Details';

const FormattedNumber = ReactIntl.FormattedNumber;

export default function({ data }) {
  const [products] = useState(data);
  const [productOpened, setProductOpened] = useState({});

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'product.name', 
      label: 'Produto',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'option.name', 
      label: 'Opção',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'price_unity', 
      label: 'UND',
      options: {
        filterType: 'textField',
        customBodyRender: (value) => {
          return <FormattedNumber style="currency" value={value} currency="BRL" />
        }
      },
    },
    {  
      name: 'quantity', 
      label: 'Quantidade',
      options: {
        filterType: 'textField'
      }
    },
    {  
      name: 'total_price', 
      label: 'Total',
      options: {
        filterType: 'textField',
        customBodyRender: (value) => {
          return <FormattedNumber style="currency" value={value} currency="BRL" />
        }
      },
    },
    {
      name: "Ação",
      options: {
        filter: false,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta) => {
          const selected = products[tableMeta.rowIndex];
          return (
              <Button onClick={() => setProductOpened(selected)}>
                  <VisibilityIcon />
              </Button>
          );
        }
      }    
    }    
  ];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: products.length,
      selectableRows: 'none'
  };

  const handleClose = () => {
    setProductOpened({});
  };

  return (
    <>
      {productOpened 
        && <Details 
        open={productOpened.id ? true : false} 
        data={productOpened} 
        handleClose={handleClose} 
      />}
        <PageTitle title="Produtos" variant="h4" />
        <MUIDataTable
          title="Lista de produtos"
          data={products}
          columns={columns}
          options={options}
        />
    </>
  );
}
