import React, { useState, useEffect } from 'react'
import TextField from '@material-ui/core/TextField';
import { useParams } from 'react-router-dom';
import Fab from '@material-ui/core/Fab';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import AddIcon from '@material-ui/icons/Add';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';
import DeleteIcon from '@material-ui/icons/Delete';

import IconButton from '@material-ui/core/IconButton';
import MoreVertIcon from '@material-ui/icons/MoreVert';

import PageTitle from "../../../components/PageTitle";
import useStyles from './styles';
import { useAlert } from "react-alert";
import Loading from '../../../components/Loading';

import Settings from './settings';

import api from '../../../services/api';

export default (props) => {
  const alert = useAlert();
  const classes = useStyles();
  const [product, setProduct] = useState({});
  const [groups, setGroups] = useState([]);

  const { id } = useParams();

  useEffect(() => {
    async function getProduct() {
        const { data } = await api.get(`/product/${id}`);
        setProduct(data);
        setGroups(data.groups);
    }

    getProduct();
  }, [id]);

  async function createGroup() {
    const { data } = await api.post(`/product/${id}/group`);
    const items = groups.slice();

    data.options = [];
    items.push(data)
    setGroups(items);
  }

  async function deleteGroup(group) {
    if (!window.confirm('Remover esse grupo?')) {
      return;
    }

    await api.delete(`/product/${id}/group/${group.id}`);
    const index = groups.indexOf(group);
    groups.splice(index, 1);
    setGroups(groups.slice());
    alert.success('Grupo excluido.');
  }

  async function updateGroup(group) {
    await api.patch(`/product/${id}/group/${group.id}`, group);
  }

  async function createOption(group) {
    const { data } = await api.post(`/product/${id}/group/${group.id}/option`);
    group.options.push(data)
    setGroups(groups.slice());
  }

  async function deleteOption(group, option) {
    await api.delete(`/product/${id}/group/${group.id}/option/${option.id}`);
    const options = group.options
    options.splice(options.indexOf(option), 1)
    setGroups(groups.slice());
    alert.success('Opção excluida.');   
  }

  async function updateOption(option) {
    await api.patch(`/product/${id}/group/${option.product_group_id}/option/${option.id}`, option);
  }

  async function showSettings(group) {
    group.open = !group.open;
    setGroups(groups.slice());
    console.log('aki')
  }

  if (!product) return <Loading />;

  return (
    <div className={classes.root}>

      <PageTitle variant={props.title ? 'h3' : 'h1'} title={props.title || 'Adicionar Opções'} subtitle={product.name}>
          <Button variant="contained" size="large" color="secondary" onClick={createGroup}>
             Adicionar Grupo
          </Button>
      </PageTitle>

      <Grid container spacing={3}>
      {groups.length > 0 && groups.map((group, i) => 
          <Grid item xs={6} key={i}>
            <Paper className={classes.paper} style={{ position: 'relative', paddingBottom: 60 }}>

              <IconButton className={classes.options} onClick={() => showSettings(group)}>
                  <MoreVertIcon />
              </IconButton>

              <Grid container style={{ paddingBottom: 20}}>
                <Grid item xs={12}>
                  <TextField 
                    className={classes.textField} label="Nome do Grupo" 
                    defaultValue={group.name} 
                    onChange={(e) => {group.name = e.target.value}}
                    onBlur={() => updateGroup(group)}
                  />
                </Grid>
              </Grid>

              {group.options.length > 0 && group.options.map((option, key) => 
                <Grid container key={key} spacing={0} style={{ marginBottom: 10 }}>
                  <Grid item xs={6}>
                    <TextField 
                      className={classes.textField} 
                      label="Nome" 
                      defaultValue={option.name} 
                      onChange={(e) => {option.name = e.target.value}} 
                      onBlur={() => updateOption(option)}
                    />
                  </Grid>
                  <Grid item xs={4}>
                    <TextField 
                      className={classes.textField} 
                      label="Preço" 
                      defaultValue={option.price} 
                      onChange={(e) => {option.price = e.target.value}} 
                      onBlur={() => updateOption(option)}
                    />
                  </Grid>

                  <Grid item xs={2}>
                    <Button onClick={() => deleteOption(group, option)}>
                      <RemoveCircleOutlineIcon />
                    </Button>
                  </Grid>

                </Grid>
              )}

              <Fab size="small" className={classes.fabDeleteGroup} color="secondary" onClick={() => deleteGroup(group)}>
                <DeleteIcon />
              </Fab>
              <Fab size="small" className={classes.fab} color="primary" onClick={() => createOption(group)}>
                <AddIcon />
              </Fab>

              <Settings showSettings={showSettings} updateGroup={updateGroup} group={group} />
              
            </Paper>
   
          </Grid>
      )}
      </Grid>
    </div>
  );

}