import React, { useEffect, useState } from 'react';

import Widget from '../../../components/Widget/Widget';
import { Typography } from "../../../components/Wrappers";

import api from '../../../services/api';
import useStyles from "../styles";
import pusher from '../../../services/pusher';

export default function() {
    const [setting, setSetting] = useState({});

    const classes = useStyles();


    useEffect(() => {
        async function fetchData() {
            const { data } = await api.get(`/setting/getAll`);
            setSetting(data);
        }

        pusher.bind(`updatedSettings`, () => {
            fetchData();
        });

        fetchData();
    }, []);

    async function setOffline() {
        const value = setting.store_status.value === "0" ? 1 : "0";
        await api.patch(`/setting/${setting.store_status.id}`, { ...setting.store_status, value });
    }

    if (!setting.store_status) return <div />;

    return (
        <Widget
            title="Status da Loja"
            upperTitle
            bodyClass={classes.fullHeightBody}
            className={classes.card}
          >
            <div className={classes.visitsNumberContainer} onClick={setOffline}>
              <Typography size="xl" weight="medium">
                  {setting.store_status.value === "0" ? 'Offline' : 'Online'}
              </Typography>
            </div>
        </Widget>        
    );

    return <div />

}