import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { CircularProgress, Button } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import EditIcon from '@material-ui/icons/Edit';
import { LinkContainer } from 'react-router-bootstrap';
import { useAlert } from "react-alert";

import TableOptions from '../../../../config/table';

import api from '../../../../services/api';

export default function({ userId }) {
  const alert = useAlert();
  const [loading, setLoading] = useState(true);
  const [addresses, setAddresses] = useState([]);

  const columns = [
    {  
      name: 'id', 
      label: 'ID',
      options: {
        filterType: 'textField'
      }
    },    
    {  
      name: 'city.name', 
      label: 'Cidade',
      options: {
        filterType: 'textField'
      }
    },
    {  
        name: 'city.state.uf', 
        label: 'UF',
        options: {
          filterType: 'textField'
        }
      },    
    {  
      name: 'street', 
      label: 'Rua',
      options: {
        filterType: 'textField'
      }
    }, 
    {  
        name: 'number', 
        label: 'Número',
        options: {
          filterType: 'textField'
        }
      }, 
    {  
        name: 'neighborhood.name', 
        label: 'Bairro',
        options: {
          filterType: 'textField'
        }
      },    
    // {
    //   name: "Ação",
    //   options: {
    //     filter: false,
    //     sort: false,
    //     empty: true,
    //     customBodyRender: (value, tableMeta) => {
    //       const selected = addresses[tableMeta.rowIndex];
    //       return (
    //         <>
    //           <LinkContainer to={`/app/user/edit/${selected.id}`}>
    //             <Button>
    //                 <EditIcon />
    //             </Button>
    //           </LinkContainer>
    //           <Button onClick={async () => {
    //             if (window.confirm('Remover esse item?')) {
    //               await deleteItem(selected.id);
    //               addresses.splice(tableMeta.rowIndex, 1);
    //               setAddresses(addresses.slice());
    //               alert.success('Item excluido com sucesso!');
    //             }
    //           }}>
    //             <HighlightOffIcon />
    //           </Button>              
    //         </>
    //       );
    //     }
    //   }
    // }    
  ];

  let itemsSelecteds = [];

  const options = {
      ...TableOptions, 
      serverSide: false,
      count: addresses.length || 0,
      onTableChange: (action, tableState) => {
        if (action === 'rowsSelect') {
          itemsSelecteds = [];

          tableState.selectedRows.data.map(data => {
            const selected = addresses[data.index];
            itemsSelecteds.push(selected);
            return data;
          });
        }

        if (action === 'rowDelete') {
          itemsSelecteds.map(address => {
            deleteItem(address.id);
            alert.success('Registros selecionados excluidos com sucesso!');
            return null;
          });
        }
      },
  };

  const deleteItem = async (id) => {
    await api.delete(`/address/${id}`);
  }

  useEffect(() => {
    async function fetchData() {
      setLoading(true);

      const { data } = await api.get(`/user/${userId}/address`);
      setAddresses(data);
      setLoading(false);
    }

    fetchData();
  }, []);

  return (
    <>
      {loading && <CircularProgress size={24} style={{marginLeft: 15, position: 'relative', top: 4}} />}

      {!loading && 
        <MUIDataTable title="Endereços do cliente" data={addresses} columns={columns} options={options} />
      }
    </>
  );
}
