import React, { useState, useEffect } from 'react';
import { useAlert } from "react-alert";
import { LinkContainer } from 'react-router-bootstrap';
import { Redirect, useParams } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import objectToFormData from 'object-to-formdata';
import { TextField, Button, FormControl, Grid } from '@material-ui/core';  
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import PageTitle from "../../../components/PageTitle";
import Loading from '../../../components/Loading';
import useStyles from './styles';

import api from '../../../services/api';

export default () => {
  const alert = useAlert();
  // const [loading, setLoading] = useState(false);
  const [loadingData, setLoadingData] = useState(true);
  const [ category, setCategory] = useState({});
  const [redirect, setRedirect] = useState(false);
  const { id } = useParams();
  const classes = useStyles();


  useEffect(() => {
    async function fetchData() {
      const { data } = await api.get(`/category/${id}`);
      setCategory(data);
      setLoadingData(false);
    }
    
    fetchData();
  }, [id])
  
  const handleOnSubmit = async (values) => { 
    try {
      const options = { indices: true, nullsAsUndefineds: false };
      const formData = objectToFormData(values, options);
      await api.patch(`/category/${id}`, formData);
      
      alert.success('Salvo com sucesso!');
      setRedirect(true);
    } catch (error) {
      alert.error(error.message);
    }
  }
  
  if (redirect) return  <Redirect to={`/app/category/list`} />;

  if (loadingData) return <Loading />;

  return (
    <div className={classes.root}>
      
      <PageTitle title='Editar Categoria' />

        <form onSubmit={handleOnSubmit}>
          <Paper className={classes.paper}>
              <FormControl style={{width: '100%'}}>
                <TextField 
                    className={classes.textField}
                    label="Informe o nome da categoria" 
                    name="name"
                    onChange={e => category.name = e.target.value}
                    defaultValue={category.name}
                />
              </FormControl>
              <FormControl style={{width: '100%'}}>
                <TextField 
                    className={classes.textField}
                    multiline
                    rows="4"
                    label="Descrição" 
                    onChange={e => category.description = e.target.value}
                    defaultValue={category.description}
                />
              </FormControl>

              <Grid container>
                {/* {category.image_url && <Grid item style={{ marginRight: 20 }}>
                  <img src={category.image_url} alt="image" title="image" />
                </Grid>
                } */}

                <Grid item>
                  <FormControl style={{width: '100%'}}>
                    <TextField 
                        className={classes.textField}
                        label="Imagem Destaque" 
                        type="file"
                        onChange={e => category.image = e.target.files[0]}
                        InputLabelProps={{
                          shrink: true,
                        }}
                    />
                </FormControl>
                </Grid>
              </Grid>

          </Paper>

          <div className={classes.buttons}>
            <LinkContainer to={'/app/category/list'}>
              <Button  variant="contained">
                <ArrowBackIosIcon /> Voltar
              </Button>
            </LinkContainer>
            <Button type="button" onClick={() => handleOnSubmit()} variant="contained" color="primary">
              Salvar <ArrowForwardIosIcon />
            </Button>
          </div>
        </form>
    </div>
  );

}