import React, { useState } from "react";
import { useDispatch, useSelector } from 'react-redux';
import { AppBar, Toolbar, IconButton, InputBase, Menu, MenuItem, Fab, Link } from "@material-ui/core";
import {
  Menu as MenuIcon,
  MailOutline as MailIcon,
  NotificationsNone as NotificationsIcon,
  Person as AccountIcon,
  Search as SearchIcon,
  Send as SendIcon,
} from "@material-ui/icons";
import classNames from "classnames";

import Orders from './Orders';

// components
import { Badge, Typography } from "../Wrappers/Wrappers";
import Notification from "../Notification";
import UserAvatar from "../UserAvatar/UserAvatar";
// styles
import useStyles from "./styles";

// context
import { useLayoutState, useLayoutDispatch, toggleSidebar } from "../../context/LayoutContext";
import { logout } from  '../../store/actions/auth';
import { LinkContainer } from "react-router-bootstrap";
import { Redirect } from "react-router-dom";

const messages = [
  {
    id: 0,
    variant: "warning",
    name: "Cleomar Campos",
    message: "Exemplo de mensagem",
    time: "9:32",
  }
];

export default function Header(props) {
  var classes = useStyles();
  const dispatch = useDispatch();
  const auth = useSelector(state => state.auth);

  // global
  var layoutState = useLayoutState();
  var layoutDispatch = useLayoutDispatch();

  // local
  var [mailMenu, setMailMenu] = useState(null);
  var [isMailsUnread, setIsMailsUnread] = useState(true);
  var [notificationsMenu, setNotificationsMenu] = useState(null);
  var [profileMenu, setProfileMenu] = useState(null);
  var [isSearchOpen, setSearchOpen] = useState(false);
  const [totalOrders, setTotalOrders] = useState(0);
  const [redirect, setRedirect] = useState(null);

  const { user } = useSelector(state => state.auth);

  if (redirect) return <Redirect to={redirect} />

  return (
    <AppBar position="fixed" className={classes.appBar}>
      <Toolbar className={classes.toolbar}>
        <IconButton
          color="inherit"
          onClick={() => toggleSidebar(layoutDispatch)}
          className={classNames(
            classes.headerMenuButton,
            classes.headerMenuButtonCollapse,
          )}
        >
          {layoutState.isSidebarOpened ? (
            <MenuIcon
              classes={{
                root: classNames(
                  classes.headerIcon,
                  classes.headerIconCollapse,
                ),
              }}
            />
          ) : (
            <MenuIcon
              classes={{
                root: classNames(
                  classes.headerIcon,
                  classes.headerIconCollapse,
                ),
              }}
            />
          )}
        </IconButton>
        <Typography variant="h6" weight="medium" className={classes.logotype}>
          Monka
        </Typography>
        <div className={classes.grow} />

        <IconButton
          color="inherit"
          aria-haspopup="true"
          aria-controls="mail-menu"
          onClick={e => {
            setNotificationsMenu(e.currentTarget);
          }}
          className={classes.headerMenuButton}
        >
          <Badge
            badgeContent={totalOrders}
            color="warning"
          >
            <NotificationsIcon classes={{ root: classes.headerIcon }} />
          </Badge>
        </IconButton>
        
        <IconButton
          aria-haspopup="true"
          color="inherit"
          className={classes.headerMenuButton}
          aria-controls="profile-menu"
          onClick={e => setProfileMenu(e.currentTarget)}
        >
          <AccountIcon classes={{ root: classes.headerIcon }} />
        </IconButton>

        <Orders 
            setNotificationsMenu={setNotificationsMenu} 
            notificationsMenu={notificationsMenu}
            anchorEl={notificationsMenu}
            onClose={() => setNotificationsMenu(null)}
            setTotalOrders={setTotalOrders}
        />

        <Menu
          id="profile-menu"
          open={Boolean(profileMenu)}
          anchorEl={profileMenu}
          onClose={() => setProfileMenu(null)}
          className={classes.headerMenu}
          classes={{ paper: classes.profileMenu }}
          disableAutoFocusItem
        >
          <div className={classes.profileMenuUser}>
            <Typography variant="h4" weight="medium">
              {auth.user.name}
            </Typography>
            <Typography
              className={classes.profileMenuLink}
              component="a"
              color="primary"
              href="/admin/user"
            >
              Monka.net.br
            </Typography>
          </div>

          <MenuItem
            className={classNames(
              classes.profileMenuItem,
              classes.headerMenuItem,
            )}
          >
            <AccountIcon className={classes.profileMenuIcon} />
            <Typography
              className={classes.profileMenuLink}
              component="a"
              color="primary"
              href={`#/app/user/detail/${user.id}`}
            >
              Meu Perfil
            </Typography>            
          </MenuItem>
          
          <div className={classes.profileMenuUser}>
            <Typography
              className={classes.profileMenuLink}
              color="primary"
              onClick={() => {
                dispatch(logout());
              }}
            >
              Sair
            </Typography>
          </div>
        </Menu>
      </Toolbar>
    </AppBar>
  );
}
