import React, { useState, useEffect } from 'react';
import { useAlert } from "react-alert";
import { Redirect } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { withFormik } from 'formik';
import moment from 'moment';
import * as Yup from 'yup';

import PageTitle from "../../../components/PageTitle";

import api from '../../../services/api';
import useStyles from './styles';
import { LinkContainer } from 'react-router-bootstrap';

function VoucherForm(props){
    const classes = useStyles();
    const alert = useAlert();
    const [users, setUsers] = useState([]);
    const [redirect, setRedirect] = useState(false);

    useEffect(() => {
        if (props.voucher) {
            props.setFieldValue('id', props.voucher.id);
            props.setFieldValue('user_id', props.voucher.user_id);
            props.setFieldValue('name', props.voucher.name);
            props.setFieldValue('code', props.voucher.code);
            props.setFieldValue('limit', props.voucher.limit);
            props.setFieldValue('limit_by_user', props.voucher.limit_by_user);
            props.setFieldValue('discount_type', props.voucher.discount_type);
            props.setFieldValue('discount_value', props.voucher.discount_value);
            props.setFieldValue('free_shipping', props.voucher.free_shipping || 0);
            props.setFieldValue('visible', props.voucher.visible);
            props.setFieldValue('active', props.voucher.active);
            props.setFieldValue('description', props.voucher.description || '');
            props.setFieldValue('date_start', new moment(props.voucher.date_start).format('YYYY-MM-DD') || '');
            props.setFieldValue('date_end', new moment(props.voucher.date_start).format('YYYY-MM-DD') || '');
        }
    }, []);

    useEffect(() => {
        async function getUsers() {
            const { data } = await api.get('/user');
            setUsers(data.data);
        }

        getUsers();
    }, []);

    useEffect(() => {
        if (props.status && props.status.success) {
            alert.show('Salvo com sucesso!');
            setRedirect(true);
        }
    }, [props.status]);

    if (redirect) return <Redirect to={`/app/voucher/list`} />

    return (
        <div className={classes.root}>
        
            <PageTitle title={props.pageTitle} />
            
            <form onSubmit={props.handleSubmit}>
                <Paper className={classes.paper}>

                    <FormControl className={classes.formControl}>
                        <InputLabel>Cliente</InputLabel>
                        <Select
                            value={props.values.user_id}
                            onChange={e => props.setFieldValue('user_id', e.target.value)}
                            InputLabelProps={{ shrink: true }}
                            style={{ textAlign: 'left' }}
                        >   
                            <option value="">--- todos ---</option>
                            {users.map(user => 
                                <option key={user.id} value={user.id}>{user.name}</option>
                            )}
                        </Select>
                        {props.touched.user_id && props.errors.user_id && <FormHelperText className={classes.error}>{props.errors.user_id}</FormHelperText>}
                    </FormControl>

                    <FormControl className={classes.formControl}>
                        <InputLabel>Status</InputLabel>
                        <Select
                            native
                            value={props.values.active}
                            onChange={e => props.setFieldValue('active', e.target.value)}
                        >
                            <option value={0}>Inativo</option>
                            <option value={1}>Ativo</option>
                        </Select>
                        {props.touched.active && props.errors.active && <FormHelperText className={classes.error}>{props.errors.active}</FormHelperText>}
                    </FormControl>
                    
                    <FormControl className={classes.formControl}>
                        <InputLabel>Frete Grátis</InputLabel>
                        <Select
                            native
                            value={props.values.free_shipping}
                            onChange={e => props.setFieldValue('free_shipping', e.target.value)}
                        >
                            <option value={1}>Não</option>
                            <option value={0}>Sim</option>
                        </Select>
                        {props.touched.free_shipping && props.errors.free_shipping && <FormHelperText className={classes.error}>{props.errors.free_shipping}</FormHelperText>}
                    </FormControl>
                                    
                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            value={props.values.name}
                            label="Nome da promoção" 
                            onChange={e => props.setFieldValue('name', e.target.value)}
                        />
                        {props.touched.name && props.errors.name && <FormHelperText className={classes.error}>{props.errors.name}</FormHelperText>}
                    </FormControl>

                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            value={props.values.code}
                            label="Código" 
                            onChange={e => props.setFieldValue('code', e.target.value)}
                        />
                        {props.touched.code && props.errors.code && <FormHelperText className={classes.error}>{props.errors.code}</FormHelperText>}
                    </FormControl>

                    <Grid container spacing={3}>
                        <Grid item xs={6}>
                            <FormControl className={classes.formControl}>
                                <TextField
                                    className={classes.textField}
                                    value={props.values.date_start}
                                    label="Data de Início" 
                                    onChange={e => props.setFieldValue('date_start', e.target.value)}
                                    type="date"
                                    InputLabelProps={{ shrink: true }}
                                />
                                {props.touched.date_start && props.errors.date_start && <FormHelperText className={classes.error}>{props.errors.date_start}</FormHelperText>}
                            </FormControl>
                        </Grid>
                        <Grid item xs={6}>
                            <FormControl className={classes.formControl}>
                                <TextField
                                    className={classes.textField}
                                    value={props.values.date_end}
                                    label="Data de Fim" 
                                    onChange={e => props.setFieldValue('date_end', e.target.value)}
                                    type="date"
                                    InputLabelProps={{ shrink: true }}
                                />
                                {props.touched.date_end && props.errors.date_end && <FormHelperText className={classes.error}>{props.errors.date_end}</FormHelperText>}
                            </FormControl>
                        </Grid>
                    </Grid>


                    <Grid container spacing={3}>
                        <Grid item xs={6}>
                            <FormControl className={classes.formControl}>
                                <TextField
                                    className={classes.textField}
                                    label="Limite de Voucher" 
                                    value={props.values.limit}
                                    onChange={e => props.setFieldValue('limit', e.target.value)}
                                    type="number"
                                />
                                {props.touched.limit && props.errors.limit && <FormHelperText className={classes.error}>{props.errors.limit}</FormHelperText>}
                            </FormControl>
                        </Grid>
                        <Grid item xs={6}>
                            <FormControl className={classes.formControl}>
                                <TextField
                                    className={classes.textField}
                                    label="Limite por Cliente" 
                                    value={props.values.limit_by_user}
                                    onChange={e => props.setFieldValue('limit_by_user', e.target.value)}
                                    type="number"
                                />
                                {props.touched.limit_by_user && props.errors.limit_by_user && <FormHelperText className={classes.error}>{props.errors.limit_by_user}</FormHelperText>}
                            </FormControl>
                        </Grid>
                    </Grid>

                    <Grid container spacing={3}>
                        <Grid item xs={6}>
                            <FormControl className={classes.formControl}>
                                <InputLabel>Tipo de Desconto</InputLabel>
                                <Select
                                    native
                                    value={props.values.discount_type}
                                    onChange={e => props.setFieldValue('discount_type', e.target.value)}
                                >
                                <option value={'percentage'}>Porcentagem</option>
                                <option value={'money'}>Reais</option>
                                </Select>
                                {props.touched.discount_type && props.errors.discount_type && <FormHelperText className={classes.error}>{props.errors.discount_type}</FormHelperText>}
                            </FormControl>
                        </Grid>
                        <Grid item xs={6}>
                            <FormControl className={classes.formControl}>
                                <TextField
                                    className={classes.textField}
                                    label="Valor de desconto" 
                                    value={props.values.discount_value}
                                    onChange={e => props.setFieldValue('discount_value', e.target.value)}
                                />
                                {props.touched.discount_value && props.errors.discount_value && <FormHelperText className={classes.error}>{props.errors.discount_value}</FormHelperText>}
                            </FormControl>
                        </Grid>
                    </Grid>

                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            label="Descrição" 
                            value={props.values.description}
                            rows={5}
                            multiline
                            onChange={e => props.setFieldValue('price', e.target.value)}
                        />
                        {props.touched.description && props.errors.description && <FormHelperText className={classes.error}>{props.errors.description}</FormHelperText>}
                    </FormControl>

                </Paper>

                <div className={classes.buttons}>
                    <LinkContainer to={'/app/voucher/list'}>
                        <Button variant="contained">
                            <ArrowBackIosIcon /> Voltar
                        </Button>
                    </LinkContainer>
                    <Button type="submit" variant="contained" color="primary">
                        Salvar <ArrowForwardIosIcon />
                    </Button>
                </div>
            </form>
        </div>
    );

}

export default withFormik({
    mapPropsToValues: props => ({ 
        id: '',
        user_id: '',
        name: 'teste',
        code: 'teste',
        limit: 1,
        limit_by_user: 1,
        discount_type: 'money',
        discount_value: 0,
        free_shipping: 0,
        visible: 1,
        active: 1,
        date_start: '2020-02-12',
        date_end: '2020-02-12',
    }),

    validationSchema: Yup.object().shape({
      name: Yup.string().required('Informe o nome da promoção'),
      code: Yup.string().required('Informe o código do voucher'),
      limit: Yup.number().required('Esse campo é obrigatório'),
      limit_by_user: Yup.number().required('Esse campo é obrigatório'),
      discount_type: Yup.string().required('Esse campo é obrigatório'),
      free_shipping: Yup.boolean().required('Esse campo é obrigatório'),
      visible: Yup.boolean().required('Esse campo é obrigatório'),
      active: Yup.boolean().required('Esse campo é obrigatório'),
      date_start: Yup.date().required('Esse campo é obrigatório'),
      date_end: Yup.date().required('Esse campo é obrigatório'),
    }),
  
    handleSubmit: async (values, { setSubmitting, setErrors, props, setStatus }) => {
        try {
            setSubmitting(true);

            let result = {};
            if (values.id) {
                result = await api.patch(`/voucher/${values.id}`, values);
            } else {
                result = await api.post(`/voucher`, values);
            }

          if (result.data.success) {
              setSubmitting(false);
              setStatus({ success: true, data: values });
          }
  
          if (result.data.validations) {
              result.data.validations.map(e => {
                  let error = {};
                  error[e.field] = e.message;
                  setErrors(error);
              });
          }
  
        } catch(error) {
          console.log(error)
          setErrors({ error: 'Não foi possível salvar os dados. Tente novamente.' });
        }
    },
  
  })(VoucherForm);
  