import React, { useState, useEffect } from 'react';
import { useAlert } from "react-alert";
import { Redirect } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Chip from '@material-ui/core/Chip';
import Input from '@material-ui/core/Input';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { withFormik } from 'formik';
import * as Yup from 'yup';

import PageTitle from "../../../components/PageTitle";

import api from '../../../services/api';
import useStyles from './styles';
import { LinkContainer } from 'react-router-bootstrap';

function NeighborhoodForm(props){
    const classes = useStyles();
    const alert = useAlert();
    const [redirect, setRedirect] = useState(false);

    useEffect(() => {
        if (props.neighborhood) {
            props.setFieldValue('name', props.neighborhood.name);
            props.setFieldValue('price', props.neighborhood.price);
            props.setFieldValue('description', props.neighborhood.description || '');
        }
    }, []);

    useEffect(() => {
        if (props.status && props.status.success) {
            alert.show('Salvo com sucesso!');
            setRedirect(true);
        }
    }, [props.status]);

    if (redirect) return<Redirect to={`/app/neighborhood/list`} />

    return (
        <div className={classes.root}>
        
            <PageTitle title={props.pageTitle} />
            
            <form onSubmit={props.handleSubmit}>
                <Paper className={classes.paper}>

                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            value={props.values.name}
                            label="Nome do bairro" 
                            name="name"
                            onChange={e => props.setFieldValue('name', e.target.value)}
                        />
                        {props.touched.name && props.errors.name && 
                            <FormHelperText className={classes.error}>{props.errors.name}</FormHelperText>
                        }
                    </FormControl>

                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            label="Preço" 
                            name="price"
                            value={props.values.price}
                            onChange={e => props.setFieldValue('price', e.target.value)}
                        />
                        {props.touched.price && props.errors.price && 
                            <FormHelperText className={classes.error}>{props.errors.price}</FormHelperText>
                        }
                    </FormControl>

                    <FormControl className={classes.formControl}>
                        <TextField
                            className={classes.textField}
                            label="Descrição" 
                            name="description"
                            value={props.values.description}
                            rows={5}
                            multiline
                            onChange={e => props.setFieldValue('price', e.target.value)}
                        />
                        {props.touched.description && props.errors.description && 
                            <FormHelperText className={classes.error}>{props.errors.description}</FormHelperText>
                        }
                    </FormControl>

                </Paper>

                <div className={classes.buttons}>
                    <LinkContainer to={'/app/neighborhood/list'}>
                        <Button variant="contained">
                            <ArrowBackIosIcon /> Voltar
                        </Button>
                    </LinkContainer>
                    <Button type="submit" variant="contained" color="primary">
                        Salvar <ArrowForwardIosIcon />
                    </Button>
                </div>
            </form>
        </div>
    );

}

export default withFormik({
    mapPropsToValues: props => ({ 
        id: '',
        name: '',
        description: '',
        observation: '',
        price: ''
    }),

    validationSchema: Yup.object().shape({
      name: Yup.string()
        .required('Informe o nome do bairro'),
      price: Yup.string()
        .required('Informe o preço da taxa de entrega'),
    }),
  
    handleSubmit: async (values, { setSubmitting, setErrors, props, setStatus }) => {
        try {
            setSubmitting(true);

            let result = {};
            if (values.id) {
                result = await api.patch(`/neighborhood/${values.id}`, values);
            } else {
                result = await api.post(`/neighborhood`, values);
            }

          if (result.data.success) {
              setSubmitting(false);
              setStatus({ success: true, data: values });
          }
  
          if (result.data.validations) {
              result.data.validations.map(e => {
                  let error = {};
                  error[e.field] = e.message;
                  setErrors(error);
              });
          }
  
        } catch(error) {
          console.log(error)
          setErrors({ error: 'Não foi possível salvar os dados. Tente novamente.' });
        }
    },
  
  })(NeighborhoodForm);
  