import React, { useState, useEffect } from "react";
import { LinkContainer } from 'react-router-bootstrap';
import { Menu, MenuItem } from "@material-ui/core";
import classNames from "classnames";

import { Typography } from "../../Wrappers/Wrappers";
import UserAvatar from "../../UserAvatar/UserAvatar";

import api from '../../../services/api';
import pusher from '../../../services/pusher';

import useStyles from "../styles";

export default function(props) {
    var [notifications, setNotifications] = useState([]);
    var classes = useStyles();
    
    useEffect(() => {
        async function getOrders() {
            const { data } = await api.get(`/order?is_opened=0&limit=10`);
    
            const newNofifications = data.map(order => {
              return {
                id: order.id,
                color: 'warning',
                type: 'offer',
                name: order.user.name,
                time: order.created_at,
                message: `Novo pedido`
              };
            });

            setNotifications(newNofifications);
            props.setTotalOrders(data.length);
        }
    
        pusher.bind(`adminNoticationsUpdate`, () => {
          getOrders();
        });

        pusher.bind(`newOrder`, () => {
          playSound();
          getOrders();
        });
        
        getOrders();
    }, []);

    function playSound() {
      const audioEl = document.getElementsByClassName("audio-element")[0]
      audioEl.play()      
    }

    return (
      <>
        <audio className="audio-element">
          <source src="https://api.coderrocketfuel.com/assets/pomodoro-times-up.mp3"></source>
        </audio>

        <Menu
          id="notifications-menu"
          open={Boolean(props.notificationsMenu)}
          anchorEl={props.anchorEl}
          onClose={props.onClose}
          className={classes.headerMenu}
          disableAutoFocusItem
        >
          <div className={classes.profileMenuUser}>
            <Typography variant="h4" weight="medium">
              Vendas
            </Typography>
            <Typography
              className={classes.profileMenuLink} component="a" color="secondary">
              {notifications.length} Novas mensage{notifications.length > 1 ? 'ns' : 'm'}
            </Typography>
          </div>

          {notifications.map(order => (
            <LinkContainer key={order.id} to={`/app/order/detail/${order.id}`} onClick={props.onClose}>
                <MenuItem className={classes.messageNotification}>
                    <div className={classes.messageNotificationSide}>
                        <UserAvatar color={order.variant} name={order.name} />
                        <Typography size="sm" color="text" colorBrightness="secondary">
                            {order.time}
                        </Typography>
                    </div>
                    <div
                        className={classNames(
                            classes.messageNotificationSide,
                            classes.messageNotificationBodySide,
                        )}
                    >
                        <Typography weight="medium" gutterBottom>
                            {order.name}
                        </Typography>
                        <Typography color="text" colorBrightness="secondary">
                            {order.message}
                        </Typography>
                    </div>
                </MenuItem>
            </LinkContainer>
          ))}
        </Menu>  
        </>      
    );

}